        "title": "Die Himmel rhmen die Herrlichkeit Gottes",
        "quote": "Das Licht aus der Hhe hat uns besucht ... um unsere Schritte zu lenken auf den Weg des Friedens. (Lk 1,78.79)",
        "text": "Die Himmel rhmen die Herrlichkeit Gottes,^*vom Werk seiner Hnde kndet das Firmament.^pEin Tag sagt es dem andern,^*eine Nacht tut es der andern kund,^pohne Worte und ohne Reden,^*unhrbar bleibt ihre Stimme.^pDoch ihre Botschaft geht in die ganze Welt hinaus,^*ihre Kunde bis zu den Enden der Erde.^pDort hat er der Sonne ein Zelt gebaut.^*Sie tritt aus ihrem Gemach hervor wie ein Brutigam;^psie frohlockt wie ein Held^*und luft ihre Bahn.^pAm einen Ende des Himmels geht sie auf^+und luft bis ans andere Ende;^*nichts kann sich vor ihrer Glut verbergen."
