        "title": "Die Weisung des Herrn erquickt den Menschen",
        "quote": "Ihr sollt vollkommen sein, denn auch euer himmlischer Vater ist vollkommen. (Mt 5,48)",
        "text": "Die Weisung des Herrn ist vollkommen und gut,^*sie erquickt den Menschen.^pDas Gesetz des Herrn ist verlsslich,^*den Unwissenden macht es weise.^pDie Befehle des Herrn sind richtig,^*sie erfreuen das Herz;^pdas Gebot des Herrn ist lauter,^*es erleuchtet die Augen.^pDie Furcht des Herrn ist rein,^*sie besteht fr immer.^pDie Urteile des Herrn sind wahr,^*gerecht sind sie alle.^pSie sind kostbarer als Gold, als Feingold in Menge.^*Sie sind ser als Honig, als Honig aus Waben.^pAuch dein Knecht lsst sich von ihnen warnen;^*wer sie beachtet, hat reichen Lohn.^pWer bemerkt seine eigenen Fehler?^*Sprich mich frei von Schuld, die mir nicht bewusst ist!^pBehte deinen Knecht auch vor vermessenen Menschen;^*sie sollen nicht ber mich herrschen.^pDann bin ich ohne Makel^*und rein von schwerer Schuld.^pDie Worte meines Mundes mgen dir gefallen;^+was ich im Herzen erwge, stehe dir vor Augen,^*Herr, mein Fels und mein Erlser."
