        "text": "Der Herr erhre dich am Tage der Not,^*der Name von Jakobs Gott mge dich schtzen.^pEr sende dir Hilfe vom Heiligtum^*und stehe dir bei vom Zion her.^pAn all deine Speiseopfer denke er,^*nehme dein Brandopfer gndig an.^pEr schenke dir, was dein Herz begehrt,^*und lasse all deine Plne gelingen.^pDann wollen wir jubeln ber deinen Sieg,^+im Namen unsres Gottes das Banner erheben.^*All deine Bitten erflle der Herr.^pNun bin ich gewiss:^*der Herr schenkt seinem Gesalbten den Sieg;^per erhrt ihn von seinem heiligen Himmel her^*und hilft ihm mit der Macht seiner Rechten.^pDie einen sind stark durch Wagen, die andern durch Rosse,^*wir aber sind stark im Namen des Herrn, unsres Gottes.^pSie sind gestrzt und gefallen;^*wir bleiben aufrecht und stehen.^pHerr, verleihe dem Knig den Sieg!^*Erhr uns am Tag, da wir rufen!"
