        "text": "An deiner Macht, Herr, freut sich der Knig;^*ber deine Hilfe, wie jubelt er laut!^pDu hast ihm den Wunsch seines Herzens erfllt,^*ihm nicht versagt, was seine Lippen begehrten.^pDu kamst ihm entgegen mit Segen und Glck,^*du krntest ihn mit einer goldenen Krone.^pLeben erbat er von dir, du gabst es ihm,^*viele Tage, fr immer und ewig.^pGro ist sein Ruhm durch deine Hilfe,^*du hast ihn bekleidet mit Hoheit und Pracht.^pDu machst ihn zum Segen fr immer;^*wenn du ihn anblickst, schenkst du ihm groe Freude.^pDenn der Knig vertraut auf den Herrn,^*die Huld des Hchsten lsst ihn niemals wanken.^pErhebe dich, Herr, in deiner Macht!^*Deiner siegreichen Kraft wollen wir singen und spielen."
