        "quote": "Durch die leibliche Aufnahme wurden Christus die Tore des Himmels geffnet. (Irenus)",
        "text": "Dem Herrn gehrt die Erde und was sie erfllt,^*der Erdkreis und seine Bewohner.^pDenn er hat ihn auf Meere gegrndet,^*ihn ber Strmen befestigt.^pWer darf hinaufziehn zum Berg des Herrn,^*wer darf stehn an seiner heiligen Sttte?^pDer reine Hnde hat und ein lauteres Herz,^*der nicht betrgt und keinen Meineid schwrt.^pEr wird Segen empfangen vom Herrn^*und Heil von Gott, seinem Helfer.^pDas sind die Menschen, die nach ihm fragen,^*die dein Antlitz suchen, Gott Jakobs.^pIhr Tore, hebt euch nach oben,^+hebt euch, ihr uralten Pforten;^*denn es kommt der Knig der Herrlichkeit.^pWer ist der Knig der Herrlichkeit?^*Der Herr, stark und gewaltig, der Herr, mchtig im Kampf.^pIhr Tore, hebt euch nach oben,^+hebt euch, ihr uralten Pforten;^*denn es kommt der Knig der Herrlichkeit.^pWer ist der Knig der Herrlichkeit?^*Der Herr der Heerscharen, er ist der Knig der Herrlichkeit."
