        "quote": "Die Hoffnung lsst nicht zugrunde gehen. (Rm 5,5)",
        "text": "Zu dir, Herr, erhebe ich meine Seele.^*Mein Gott, auf dich vertraue ich.^pLass mich nicht scheitern,^*lass meine Feinde nicht triumphieren!^pDenn niemand, der auf dich hofft, wird zuschanden;^*zuschanden wird, wer dir schnde die Treue bricht.^pZeige mir, Herr, deine Wege,^*lehre mich deine Pfade!^pFhre mich in deiner Treue und lehre mich;^+denn du bist der Gott meines Heiles.^*Auf dich hoffe ich allezeit.^pDenk an dein Erbarmen, Herr,^+und an die Taten deiner Huld;^*denn sie bestehen seit Ewigkeit.^pDenk nicht an meine Jugendsnden und meine Frevel!^*In deiner Huld denk an mich, Herr, denn du bist gtig.^pGut und gerecht ist der Herr,^*darum weist er die Irrenden auf den rechten Weg.^pDie Demtigen leitet er nach seinem Recht,^*die Gebeugten lehrt er seinen Weg.^pAlle Pfade des Herrn sind Huld und Treue^*denen, die seinen Bund und seine Gebote bewahren.^pUm deines Namens willen, Herr, verzeih mir;^*denn meine Schuld ist gro."
