        "text": "Verschaff mir Recht, o Herr; denn ich habe ohne Schuld gelebt.^*Dem Herrn habe ich vertraut, ohne zu wanken.^pErprobe mich, Herr, und durchforsche mich,^*prfe mich auf Herz und Nieren!^pDenn mir stand deine Huld vor Augen,^*ich ging meinen Weg in Treue zu dir.^pIch sa nicht bei falschen Menschen,^*mit Heuchlern hatte ich keinen Umgang.^pVerhasst ist mir die Schar derer, die Unrecht tun;^*ich sitze nicht bei den Frevlern.^pIch wasche meine Hnde in Unschuld;^*ich umschreite, Herr, deinen Altar,^pum laut dein Lob zu verknden^*und all deine Wunder zu erzhlen.^pHerr, ich liebe den Ort, wo dein Tempel steht,^*die Sttte, wo deine Herrlichkeit wohnt.^pRaff mich nicht hinweg mit den Sndern,^*nimm mir nicht das Leben zusammen mit dem der Mrder!^pAn ihren Hnden klebt Schandtat,^*ihre Rechte ist voll von Bestechung.^pIch aber gehe meinen Weg ohne Schuld.^*Erlse mich und sei mir gndig!^pMein Fu steht auf festem Grund.^*Den Herrn will ich preisen in der Gemeinde."
