        "quote": "Seht die Wohnsttte Gottes unter den Menschen. (Offb 21,3)",
        "text": "^(Der Herr ist mein Licht und mein Heil:^*Vor wem sollte ich mich frchten?^)^pDer Herr ist die Kraft meines Lebens:^*Vor wem sollte mir bangen?^pDringen Frevler auf mich ein,^*um mich zu verschlingen,^pmeine Bedrnger und Feinde,^*sie mssen straucheln und fallen.^pMag ein Heer mich belagern:^*Mein Herz wird nicht verzagen.^pMag Krieg gegen mich toben:^*Ich bleibe dennoch voll Zuversicht.^pNur eines erbitte ich vom Herrn,^*danach verlangt mich:^pIm Haus des Herrn zu wohnen^*alle Tage meines Lebens,^pdie Freundlichkeit des Herrn zu schauen^*und nachzusinnen in seinem Tempel.^pDenn er birgt mich in seinem Haus^*am Tage des Unheils;^per beschirmt mich im Schutz seines Zeltes,^*er hebt mich auf einen Felsen empor.^pNun kann ich mein Haupt erheben^*ber die Feinde, die mich umringen.^pIch will Opfer darbringen in seinem Zelt, Opfer mit Jubel;^*dem Herrn will ich singen und spielen."
