        "quote": "Einer von euch wird mich verraten und ausliefern, einer von denen, die zusammen mit mir essen. (Mk 14,18)",
        "text": "Wohl dem, der sich des Schwachen annimmt;^*zur Zeit des Unheils wird der Herr ihn retten.^pIhn wird der Herr behten^*und am Leben erhalten.^pMan preist ihn glcklich im Land.^*Gib ihn nicht seinen gierigen Feinden preis!^pAuf dem Krankenbett wird der Herr ihn strken;^*seine Krankheit verwandelst du in Kraft.^pIch sagte: Herr, sei mir gndig,^*heile mich, denn ich habe gegen dich gesndigt.^pMeine Feinde reden bse ber mich:^*Wann stirbt er endlich und wann vergeht sein Name?^pBesucht mich jemand,^*so kommen seine Worte aus falschem Herzen.^pEr huft in sich Bosheit an,^*dann geht er hinaus und redet.^pIm Hass gegen mich sind sich alle einig;^*sie tuscheln ber mich und sinnen auf Unheil:^pVerderben hat sich ber ihn ergossen;^*wer einmal daliegt, steht nicht mehr auf.^pAuch mein Freund, dem ich vertraute,^*der mein Brot a, hat gegen mich geprahlt.^pDu aber, Herr, sei mir gndig;^*richte mich auf, damit ich ihnen vergelten kann.^pDaran erkenne ich, dass du an mir Gefallen hast:^*wenn mein Feind nicht ber mich triumphieren darf.^pWeil ich aufrichtig bin, hltst du mich fest^*und stellst mich vor dein Antlitz fr immer.^pGepriesen sei der Herr, der Gott Israels,^*von Ewigkeit zu Ewigkeit! Amen, ja amen."
