        "text": "Wie der Hirsch lechzt nach frischem Wasser,^*so lechzt meine Seele, Gott, nach dir.^pMeine Seele drstet nach Gott,^*nach dem lebendigen Gott.^pWann darf ich kommen^*und Gottes Antlitz schauen?^pTrnen waren mein Brot bei Tag und bei Nacht;^*denn man sagt zu mir den ganzen Tag: Wo ist nun dein Gott?^pDas Herz geht mir ber, wenn ich daran denke:^+wie ich zum Haus Gottes zog in festlicher Schar,^*mit Jubel und Dank in feiernder Menge.^pMeine Seele, warum bist du betrbt^*und bist so unruhig in mir?^pHarre auf Gott; denn ich werde ihm noch danken,^*meinem Gott und Retter, auf den ich schaue.^pBetrbt ist meine Seele in mir, darum denke ich an dich^*im Jordanland, am Hermon, am Mizar-Berg.^pFlut ruft der Flut zu beim Tosen deiner Wasser,^*all deine Wellen und Wogen gehen ber mich hin.^pBei Tag schenke der Herr seine Huld;^*ich singe ihm nachts und flehe zum Gott meines Lebens.^pIch sage zu Gott, meinem Fels:^*Warum hast du mich vergessen?^pWarum muss ich trauernd umhergehen,^*von meinem Feind bedrngt?^pWie ein Stechen in meinen Gliedern^*ist fr mich der Hohn der Bedrnger;^pdenn sie rufen mir stndig zu:^*Wo ist nun dein Gott?^pMeine Seele, warum bist du betrbt^*und bist so unruhig in mir?^pHarre auf Gott; denn ich werde ihm noch danken,^*meinem Gott und Retter, auf den ich schaue."
