        "quote": "Er entrckte mich in der Verzckung auf einen groen, hohen Berg und zeigte mir die heilige Stadt Jerusalem. (Offb 21,10)",
        "text": "^(Gro ist der Herr und hoch zu preisen^*in der Stadt unseres Gottes.^)^pSein heiliger Berg ragt herrlich empor;^*er ist die Freude der ganzen Welt.^pDer Berg Zion liegt weit im Norden;^*er ist die Stadt des groen Knigs.^pGott ist in ihren Husern bekannt^*als ein sicherer Schutz.^pDenn seht: Die Knige vereinten sich^*und zogen gemeinsam heran;^pdoch als sie aufsahen, erstarrten sie vor Schreck,^*sie waren bestrzt und liefen davon.^pDort packte sie das Zittern,^*wie die Wehen eine gebrende Frau,^pwie der Sturm vom Osten,^*der die Schiffe von Tarschisch zerschmettert.^pWie wirs gehrt hatten, so erlebten wirs jetzt^*in der Stadt des Herrn der Heere,^pin der Stadt unseres Gottes;^*Gott lsst sie ewig bestehen.^pber deine Huld, o Gott, denken wir nach^*in deinem heiligen Tempel.^pWie dein Name, Gott, so reicht dein Ruhm bis an die Enden der Erde;^*deine rechte Hand ist voll von Gerechtigkeit.^pDer Berg Zion freue sich,^*die Tchter Judas sollen ber deine gerechten Urteile jubeln.^pUmkreist den Zion,^*umschreitet ihn, zhlt seine Trme!^pBetrachtet seine Wlle,^+geht in seinen Palsten umher,^*damit ihr dem kommenden Geschlecht erzhlen knnt:^pDas ist Gott, unser Gott fr immer und ewig.^*Er wird uns fhren in Ewigkeit."
