        "text": "Hrt dies an, ihr Vlker alle,^*vernehmt es, alle Bewohner der Erde,^pihr Leute aus dem Volk und vom Adel,^*Reiche und Arme zusammen!^pMein Mund spreche weise Worte,^*was mein Herz ersinnt, sei voller Einsicht.^pIch wende mein Ohr einem Weisheitsspruch zu,^*ich enthlle mein Geheimnis beim Harfenspiel.^pWarum soll ich mich in bsen Tagen frchten,^*wenn mich der Frevel tckischer Feinde umgibt?^pSie verlassen sich ganz auf ihren Besitz^*und rhmen sich ihres groen Reichtums.^pLoskaufen kann doch keiner den andern^*noch an Gott fr ihn ein Shnegeld zahlen^p- fr das Leben ist jeder Kaufpreis zu hoch;^*fr immer muss man davon abstehn -,^pdamit er auf ewig weiterlebt^*und niemals das Grab schaut.^pDenn man sieht: Weise sterben,^+genauso gehen Tor und Narr zugrunde,^*sie mssen andern ihren Reichtum lassen.^pDas Grab ist ihr Haus auf ewig,^+ist ihre Wohnung fr immer,^*ob sie auch Lnder nach ihren Namen benannten.^pDer Mensch bleibt nicht in seiner Pracht,^*er gleicht dem Vieh, das verstummt."
