        "text": "Der Gott der Gtter, der Herr, spricht,^+er ruft der Erde zu^*vom Aufgang der Sonne bis zum Untergang.^pVom Zion her, der Krone der Schnheit,^*geht Gott strahlend auf.^pUnser Gott kommt und schweigt nicht;^*Feuer frisst vor ihm her; um ihn strmt es gewaltig.^pDem Himmel droben und der Erde ruft er zu,^*er werde sein Volk nun richten:^pVersammelt mir all meine Frommen,^*die den Bund mit mir schlossen beim Opfer.^pDie Himmel sollen seine Gerechtigkeit knden;^*Gott selbst wird Richter sein."
