        "text": "Gott, sei mir gndig nach deiner Huld,^*tilge meine Frevel nach deinem reichen Erbarmen!^pWasch meine Schuld von mir ab^*und mach mich rein von meiner Snde!^pDenn ich erkenne meine bsen Taten,^*meine Snde steht mir immer vor Augen.^pGegen dich allein habe ich gesndigt,^*ich habe getan, was dir missfllt.^pSo behltst du recht mit deinem Urteil,^*rein stehst du da als Richter.^pDenn ich bin in Schuld geboren;^*in Snde hat mich meine Mutter empfangen.^pLauterer Sinn im Verborgenen gefllt dir,^*im Geheimen lehrst du mich Weisheit.^pEntsndige mich mit Ysop, dann werde ich rein;^*wasche mich, dann werde ich weier als Schnee.^pSttige mich mit Entzcken und Freude!^*Jubeln sollen die Glieder, die du zerschlagen hast.^pVerbirg dein Gesicht vor meinen Snden;^*tilge all meine Frevel!^pErschaffe mir, Gott, ein reines Herz^*und gib mir einen neuen, bestndigen Geist!^pVerwirf mich nicht von deinem Angesicht^*und nimm deinen heiligen Geist nicht von mir!^pMach mich wieder froh mit deinem Heil;^*mit einem willigen Geist rste mich aus!^pDann lehre ich Abtrnnige deine Wege,^*und die Snder kehren um zu dir.^pBefrei mich von Blutschuld, Herr, du Gott meines Heiles,^*dann wird meine Zunge jubeln ber deine Gerechtigkeit.^pHerr, ffne mir die Lippen,^*und mein Mund wird deinen Ruhm verknden.^pSchlachtopfer willst du nicht, ich wrde sie dir geben;^*an Brandopfern hast du kein Gefallen.^pDas Opfer, das Gott gefllt, ist ein zerknirschter Geist,^*ein zerbrochenes und zerschlagenes Herz wirst du, Gott, nicht verschmhen.^pIn deiner Huld tu Gutes an Zion;^*bau die Mauern Jerusalems wieder auf!^pDann hast du Freude an rechten Opfern,^+an Brandopfern und Ganzopfern,^*dann opfert man Stiere auf deinem Altar."
