        "quote": "Alle haben gesndigt und die Herrlichkeit Gottes verloren. (Rm 3,23)",
        "text": "Die Toren sagen in ihrem Herzen:^*Es gibt keinen Gott.^pSie handeln verwerflich und schnde;^*da ist keiner, der Gutes tut.^pGott blickt vom Himmel herab auf die Menschen,^*ob noch ein Verstndiger da ist, der Gott sucht.^pAlle sind sie abtrnnig und verdorben,^*keiner tut Gutes, auch nicht ein Einziger.^pHaben denn die beltter keine Einsicht?^*Sie verschlingen mein Volk.^pSie essen Gottes Brot,^*doch seinen Namen rufen sie nicht an.^pEs trifft sie Furcht und Schrecken,^*obwohl doch nichts zu frchten ist.^pDeinen Bedrngern hat Gott die Glieder zerschlagen.^*Gott lsst sie scheitern, denn er hat sie verworfen.^pAch kme doch vom Zion Hilfe fr Israel!^+Wenn Gott einst das Geschick seines Volkes wendet,^*dann jubelt Jakob, dann freut sich Israel."
