        "text": "Hilf mir, Gott, durch deinen Namen,^*verschaff mir Recht mit deiner Kraft!^pGott, hre mein Flehen,^*vernimm die Worte meines Mundes!^pDenn es erheben sich gegen mich stolze Menschen,^+freche Leute trachten mir nach dem Leben;^*sie haben Gott nicht vor Augen.^pGott ist mein Helfer,^*der Herr beschtzt mein Leben.^pFreudig bringe ich dir dann mein Opfer dar^*und lobe deinen Namen, Herr; denn du bist gtig.^pDer Herr hat mich herausgerissen aus all meiner Not^*und mein Auge kann auf meine Feinde herabsehn."
