        "verses": "2-7.9-14",
        "title": "Trost und Vertrauen",
        "quote": "Hier wird Christus in seiner Passion dargestellt. (Hieronymus)",
        "text": "Sei mir gndig, Gott, denn Menschen stellen mir nach;^*meine Feinde bedrngen mich Tag fr Tag.^pTglich stellen meine Gegner mir nach;^*ja, es sind viele, die mich voll Hochmut bekmpfen.^pAn dem Tag, da ich mich frchten muss,^*setze ich auf dich mein Vertrauen.^pIch preise Gottes Wort.^+Ich vertraue auf Gott und frchte mich nicht.^*Was knnen Menschen mir antun?^pSie verdrehen meine Worte den ganzen Tag;^*auf mein Verderben geht ihr ganzes Sinnen.^pSie lauern und sphen und beobachten genau meine Schritte;^*denn sie trachten mir nach dem Leben.^pMein Elend ist aufgezeichnet bei dir.^+Sammle meine Trnen in einem Krug,^*zeichne sie auf in deinem Buch!^pDie Feinde weichen zurck an dem Tag, da ich rufe.^*Ich habe erkannt: Mir steht Gott zur Seite.^pIch preise Gottes Wort,^*ich preise das Wort des Herrn.^pIch vertraue auf Gott und frchte mich nicht.^*Was knnen Menschen mir antun?^pIch schulde dir die Erfllung meiner Gelbde, o Gott;^*ich will dir Dankopfer weihen.^pDenn du hast mein Leben dem Tod entrissen,^*meine Fe bewahrt vor dem Fall.^pSo gehe ich vor Gott meinen Weg^*im Licht der Lebenden."
