        "title": "Wach auf, meine Seele",
        "quote": "Dieser Psalm besingt Christus in seiner Passion. (Augustinus)",
        "text": "Sei mir gndig, o Gott, sei mir gndig,^*denn ich flchte mich zu dir.^pIm Schatten deiner Flgel finde ich Zuflucht,^*bis das Unheil vorbergeht.^pIch rufe zu Gott, dem Hchsten,^*zu Gott, der mir beisteht.^pEr sende mir Hilfe vom Himmel;^+meine Feinde schmhen mich.^*Gott sende seine Huld und Treue.^pIch muss mich mitten unter Lwen lagern,^*die gierig auf Menschen sind.^pIhre Zhne sind Spiee und Pfeile,^*ein scharfes Schwert ihre Zunge.^pSie haben meinen Schritten ein Netz gelegt^*und meine Seele gebeugt.^pSie haben mir eine Grube gegraben;^*doch fielen sie selbst hinein.^pErheb dich ber die Himmel, o Gott!^*Deine Herrlichkeit erscheine ber der ganzen Erde.^pMein Herz ist bereit, o Gott,^+mein Herz ist bereit,^*ich will dir singen und spielen.^pWach auf, meine Seele!^+Wacht auf, Harfe und Saitenspiel!^*Ich will das Morgenrot wecken.^pIch will dich vor den Vlkern preisen, Herr,^*dir vor den Nationen lobsingen.^pDenn deine Gte reicht, so weit der Himmel ist,^*deine Treue, so weit die Wolken ziehn.^pErheb dich ber die Himmel, o Gott;^*deine Herrlichkeit erscheine ber der ganzen Erde."
