        "title": "Bitte um Hilfe nach einer Niederlage",
        "quote": "In der Welt seid ihr in Bedrngnis; aber habt Mut: Ich habe die Welt besiegt. (Joh 16,33)",
        "text": "Du hast uns verworfen, o Gott, und zerschlagen.^*Du hast uns gezrnt. Richte uns wieder auf!^pErschttert hast du das Land und gespalten.^*Heile seine Risse! Denn es kam ins Wanken.^pDu hast dein Volk hart geprft,^*du gabst uns betubenden Wein zu trinken.^pFr alle, die dich frchten, hast du ein Zeichen aufgestellt,^*zu dem sie fliehen knnen vor dem Bogen.^pHilf mit deiner Rechten, erhre uns,^*damit die gerettet werden, die du so sehr liebst.^pGott hat in seinem Heiligtum gesprochen:^+Ich will triumphieren, will Sichem verteilen^*und das Tal von Sukkot vermessen.^pMein ist Gilead, mein auch Manasse,^+Efraim ist der Helm auf meinem Haupt,^*Juda mein Herrscherstab.^pDoch Moab ist mein Waschbecken,^+auf Edom werfe ich meinen Schuh,^*ich triumphiere ber das Land der Philister.^pWer fhrt mich hin zu der befestigten Stadt,^*wer wird mich nach Edom geleiten?^pGott, hast denn du uns verworfen?^*Du ziehst ja nicht aus, o Gott, mit unseren Heeren.^pBring uns doch Hilfe im Kampf mit dem Feind!^*Denn die Hilfe von Menschen ist nutzlos.^pMit Gott werden wir Groes vollbringen;^*er selbst wird unsere Feinde zertreten."
