        "quote": "Der Gott der Hoffnung erflle euch durch euren Glauben mit allem Frieden. (Rm 15,13)",
        "text": "Bei Gott allein kommt meine Seele zur Ruhe,^*von ihm kommt mir Hilfe.^pNur er ist mein Fels, meine Hilfe, meine Burg,^*darum werde ich nicht wanken.^pWie lange rennt ihr an gegen einen Einzigen,^+strmt alle heran wie gegen eine fallende Wand,^*wie gegen eine Mauer, die einstrzt?^pJa, sie planen, ihn von seiner Hhe zu strzen;^*Lgen ist ihre Lust.^pSie segnen mit ihrem Mund,^*doch in ihrem Herzen fluchen sie.^pBei Gott allein kommt meine Seele zur Ruhe,^*denn von ihm kommt meine Hoffnung.^pNur er ist mein Fels, meine Hilfe, meine Burg;^*darum werde ich nicht wanken.^pBei Gott ist mein Heil, meine Ehre;^*Gott ist mein schtzender Fels, meine Zuflucht.^pVertrau ihm, Volk Gottes, zu jeder Zeit!^+Schttet euer Herz vor ihm aus!^*Denn Gott ist unsere Zuflucht.^pNur ein Hauch sind die Menschen,^*die Leute nur Lug und Trug.^pAuf der Waage schnellen sie empor,^*leichter als ein Hauch sind sie alle.^pVertraut nicht auf Gewalt,^*verlasst euch nicht auf Raub!^pWenn der Reichtum auch wchst,^*so verliert doch nicht euer Herz an ihn!^pEines hat Gott gesagt,^*zweierlei habe ich gehrt:^pBei Gott ist die Macht;^*Herr, bei dir ist die Huld.^pDenn du wirst jedem vergelten,^*wie es seine Taten verdienen."
