        "text": "Hre, o Gott, mein lautes Klagen,^*schtze mein Leben vor dem Schrecken des Feindes!^pVerbirg mich vor der Schar der Bsen,^*vor dem Toben derer, die Unrecht tun.^pSie schrfen ihre Zunge wie ein Schwert,^*schieen giftige Worte wie Pfeile,^pum den Schuldlosen von ihrem Versteck aus zu treffen.^*Sie schieen auf ihn, pltzlich und ohne Scheu.^pSie sind fest entschlossen zu bsem Tun.^*Sie planen, Fallen zu stellen, und sagen: Wer sieht uns schon?^pSie haben Bosheit im Sinn,^*doch halten sie ihre Plne geheim.^pIhr Inneres ist heillos verdorben,^*ihr Herz ist ein Abgrund.^pDa trifft sie Gott mit seinem Pfeil;^*sie werden jhlings verwundet.^pIhre eigene Zunge bringt sie zu Fall.^*Alle, die es sehen, schtteln den Kopf.^pDann frchten sich alle Menschen;^+sie verknden Gottes Taten^*und bedenken sein Wirken.^pDer Gerechte freut sich am Herrn und sucht bei ihm Zuflucht.^*Und es rhmen sich alle Menschen mit redlichem Herzen."
