        "quote": "Unter Zion verstehe die himmlische Stadt. (Origenes)",
        "text": "^(Dir gebhrt Lobgesang, Gott, auf dem Zion,^*dir erfllt man Gelbde.^)^pDu erhrst die Gebete:^*Alle Menschen kommen zu dir unter der Last ihrer Snden.^pUnsre Schuld ist zu gro fr uns,^*du wirst sie vergeben.^pWohl denen, die du erwhlst und in deine Nhe holst,^*die in den Vorhfen deines Heiligtums wohnen.^pWir wollen uns am Gut deines Hauses sttigen,^*am Gut deines heiligen Tempels.^pDu vollbringst erstaunliche Taten,^*erhrst uns in Treue, du Gott unsres Heiles,^pdu Zuversicht aller Enden der Erde^*und der fernsten Gestade.^pDu grndest die Berge in deiner Kraft,^*du grtest dich mit Strke.^pDu stillst das Brausen der Meere,^*das Brausen ihrer Wogen, das Tosen der Vlker.^pAlle, die an den Enden der Erde wohnen,^+erschauern vor deinen Zeichen;^*Ost und West erfllst du mit Jubel.^pDu sorgst fr das Land und trnkst es;^*du berschttest es mit Reichtum.^pDer Bach Gottes ist reichlich gefllt,^*du schaffst ihnen Korn; so ordnest du alles.^pDu trnkst die Furchen, ebnest die Schollen,^*machst sie weich durch Regen, segnest ihre Gewchse.^pDu krnst das Jahr mit deiner Gte,^*deinen Spuren folgt berfluss.^pIn der Steppe prangen die Auen,^*die Hhen umgrten sich mit Jubel.^pDie Weiden schmcken sich mit Herden,^+die Tler hllen sich in Korn.^*Sie jauchzen und singen."
