        "title": "Gottes Siegeszug vom Sinai zum Zion",
        "quote": "Er stieg hinauf zur Hhe und erbeutete Gefangene, er gab den Menschen Geschenke. (Eph 4,8)",
        "text": "^(Gott steht auf, seine Feinde zerstieben;^*die ihn hassen, fliehen vor seinem Angesicht.^)^pSie verfliegen, wie Rauch verfliegt;^+wie Wachs am Feuer zerfliet,^*so vergehen die Frevler vor Gottes Angesicht.^pDie Gerechten aber freuen sich und jubeln vor Gott;^*sie jauchzen in heller Freude.^pSingt fr Gott, spielt seinem Namen;^*jubelt ihm zu, ihm, der auf den Wolken einherfhrt!^pPreist seinen Namen!^*Freut euch vor seinem Angesicht!^pEin Vater der Waisen, ein Anwalt der Witwen^*ist Gott in seiner heiligen Wohnung.^pGott bringt die Verlassenen heim,^+fhrt die Gefangenen hinaus in das Glck;^*doch die Emprer mssen wohnen im drren Land.^pGott, als du deinem Volk voranzogst,^+als du die Wste durchschrittest,^*da bebte die Erde,^pda ergossen sich die Himmel vor Gott,^*vor Gott, dem Herrn vom Snai, vor Israels Gott.^pGott, du lieest Regen strmen in Flle^*und erquicktest dein verschmachtendes Erbland.^pDeine Geschpfe finden dort Wohnung;^*Gott, in deiner Gte versorgst du den Armen."
