        "title": "Israel, Gottes Weinstock",
        "quote": "Amen! Komm, Herr Jesus! (Offb 22,20)",
        "text": "Du Hirte Israels, hre,^*der du Josef weidest wie eine Herde!^pDer du auf den Kerubim thronst, erscheine^*vor Efraim, Benjamin und Manasse!^pBiete deine gewaltige Macht auf^*und komm uns zu Hilfe!^pGott, richte uns wieder auf!^*Lass dein Angesicht leuchten, dann ist uns geholfen.^pHerr, Gott der Heerscharen, wie lange noch zrnst du,^*whrend dein Volk zu dir betet?^pDu hast sie gespeist mit Trnenbrot,^*sie berreich getrnkt mit Trnen.^pDu machst uns zum Spielball der Nachbarn,^*und unsere Feinde verspotten uns.^pGott der Heerscharen, richte uns wieder auf!^*Lass dein Angesicht leuchten, dann ist uns geholfen.^pDu hobst in gypten einen Weinstock aus,^*du hast Vlker vertrieben, ihn aber eingepflanzt.^pDu schufst ihm weiten Raum;^*er hat Wurzeln geschlagen und das ganze Land erfllt.^pSein Schatten bedeckte die Berge,^*seine Zweige die Zedern Gottes.^pSeine Ranken trieb er hin bis zum Meer^*und seine Schlinge bis zum Eufrat.^pWarum rissest du seine Mauern ein?^*Alle, die des Weges kommen, plndern ihn aus.^pDer Eber aus dem Wald whlt ihn um,^*die Tiere des Feldes fressen ihn ab.^pGott der Heerscharen, wende dich uns wieder zu!^*Blick vom Himmel herab und sieh auf uns!^pSorge fr diesen Weinstock^*und fr den Garten, den deine Rechte gepflanzt hat.^pDie ihn im Feuer verbrannten wie Kehricht,^*sie sollen vergehen vor deinem drohenden Angesicht.^pDeine Hand schtze den Mann zu deiner Rechten,^*den Menschensohn, den du fr dich gro und stark gemacht.^pErhalt uns am Leben!^*Dann wollen wir deinen Namen anrufen und nicht von dir weichen.^pHerr, Gott der Heerscharen, richte uns wieder auf!^*Lass dein Angesicht leuchten, dann ist uns geholfen."
