        "quote": "Seht zu, dass keiner von euch ein schlechtes, unglubiges Herz hat. (Hebr 3,12)",
        "text": "^(Jubelt Gott zu, er ist unsre Zuflucht;^)^*jauchzt dem Gott Jakobs zu!^pStimmt an den Gesang, schlagt die Pauke,^*die liebliche Laute, dazu die Harfe!^pStot in die Posaune am Neumond^*und zum Vollmond, am Tag unsres Festes!^pDenn das ist Satzung fr Israel,^*Entscheid des Gottes Jakobs.^pDas hat er als Gesetz fr Josef erlassen,^*als Gott gegen gypten auszog.^pEine Stimme hre ich, die ich noch nie vernahm:^+Seine Schulter hab ich von der Brde befreit,^*seine Hnde kamen los vom Lastkorb.^pDu riefst in der Not,^*und ich riss dich heraus;^pich habe dich aus dem Gewlk des Donners erhrt,^*an den Wassern von Merba geprft.^pHre, mein Volk, ich will dich mahnen!^*Israel, wolltest du doch auf mich hren!^pFr dich gibt es keinen andern Gott.^*Du sollst keinen fremden Gott anbeten.^pIch bin der Herr, dein Gott,^+der dich heraufgefhrt hat aus gypten.^*Tu deinen Mund auf! Ich will ihn fllen.^pDoch mein Volk hat nicht auf meine Stimme gehrt;^*Israel hat mich nicht gewollt.^pDa berlie ich sie ihrem verstockten Herzen,^*und sie handelten nach ihren eigenen Plnen.^pAch dass doch mein Volk auf mich hrte,^*dass Israel gehen wollte auf meinen Wegen!^pWie bald wrde ich seine Feinde beugen,^*meine Hand gegen seine Bedrnger wenden.^pAlle, die den Herrn hassen, mssten Israel schmeicheln,^*und das sollte fr immer so bleiben.^pIch wrde es nhren mit bestem Weizen^*und mit Honig aus dem Felsen sttigen."
