        "title": "Heimweh nach dem Tempel",
        "quote": "Wir haben hier keine Stadt, die bestehen bleibt, sondern wir suchen die knftige. (Hebr 13,14)",
        "text": "Wie liebenswert ist deine Wohnung, Herr der Heerscharen!^+Meine Seele verzehrt sich in Sehnsucht^*nach dem Tempel des Herrn.^pMein Herz und mein Leib jauchzen ihm zu,^*ihm, dem lebendigen Gott.^pAuch der Sperling findet ein Haus^+und die Schwalbe ein Nest fr ihre Jungen  *^ldeine Altre, Herr der Heerscharen, mein Gott und mein Knig.^pWohl denen, die wohnen in deinem Haus,^*die dich allezeit loben.^pWohl den Menschen, die Kraft finden in dir,^*wenn sie sich zur Wallfahrt rsten.^pZiehen sie durch das trostlose Tal,^+wird es fr sie zum Quellgrund,^*und Frhregen hllt es in Segen.^pSie schreiten dahin mit wachsender Kraft;^*dann schauen sie Gott auf dem Zion.^pHerr der Heerscharen, hre mein Beten,^*vernimm es, Gott Jakobs!^pGott, sieh her auf unsern Schild,^*schau auf das Antlitz deines Gesalbten!^pDenn ein einziger Tag in den Vorhfen deines Heiligtums^*ist besser als tausend andere.^pLieber an der Schwelle stehen im Haus meines Gottes^*als wohnen in den Zelten der Frevler.^pDenn Gott der Herr ist Sonne und Schild.^*Er schenkt Gnade und Herrlichkeit;^pder Herr versagt denen, die rechtschaffen sind, keine Gabe.^+Herr der Heerscharen, wohl dem, der dir vertraut!"
