        "quote": "Als unser Erlser zur Erde hinabstieg, segnete Gott sein Land. (Origenes)",
        "text": "Einst hast du, Herr, dein Land begnadet^*und Jakobs Unglck gewendet,^phast deinem Volk die Schuld vergeben,^*all seine Snden zugedeckt,^phast zurckgezogen deinen ganzen Grimm^*und deinen glhenden Zorn gedmpft.^pGott, unser Retter, richte uns wieder auf,^*lass von deinem Unmut gegen uns ab!^pWillst du uns ewig zrnen,^*soll dein Zorn dauern von Geschlecht zu Geschlecht?^pWillst du uns nicht wieder beleben,^*sodass dein Volk sich an dir freuen kann?^pErweise uns, Herr, deine Huld^*und gewhre uns dein Heil!^pIch will hren, was Gott redet:^+Frieden verkndet der Herr seinem Volk^*und seinen Frommen, den Menschen mit redlichem Herzen.^pSein Heil ist denen nahe, die ihn frchten.^*Seine Herrlichkeit wohne in unserm Land!^pEs begegnen einander Huld und Treue;^*Gerechtigkeit und Friede kssen sich.^pTreue sprosst aus der Erde hervor;^*Gerechtigkeit blickt vom Himmel hernieder.^pAuch spendet der Herr dann Segen,^*und unser Land gibt seinen Ertrag.^pGerechtigkeit geht vor ihm her,^*und Heil folgt der Spur seiner Schritte."
