        "text": "Wende dein Ohr mir zu, erhre mich, Herr!^*Denn ich bin arm und gebeugt.^pBeschtze mich, denn ich bin dir ergeben!^*Hilf deinem Knecht, der dir vertraut!^pDu bist mein Gott. Sei mir gndig, o Herr!^*Den ganzen Tag rufe ich zu dir.^pHerr, erfreue deinen Knecht,^*denn ich erhebe meine Seele zu dir.^pHerr, du bist gtig und bereit zu verzeihen,^*fr alle, die zu dir rufen, reich an Gnade.^pHerr, vernimm mein Beten,^*achte auf mein lautes Flehen!^pAm Tag meiner Not rufe ich zu dir,^*denn du wirst mich erhren.^pHerr, unter den Gttern ist keiner wie du,^*und nichts gleicht den Werken, die du geschaffen hast.^pAlle Vlker kommen und beten dich an,^*sie geben, Herr, deinem Namen die Ehre.^pDenn du bist gro und tust Wunder;^*du allein bist Gott.^pWeise mir, Herr, deinen Weg;^*ich will ihn gehen in Treue zu dir.^pRichte mein Herz darauf hin,^*allein deinen Namen zu frchten!^pIch will dir danken, Herr, mein Gott, aus ganzem Herzen,^*will deinen Namen ehren immer und ewig.^pDu hast mich den Tiefen des Totenreichs entrissen.^*Denn gro ist ber mir deine Huld.^pGott, freche Menschen haben sich gegen mich erhoben,^+die Rotte der Gewalttter trachtet mir nach dem Leben;^*doch dich haben sie nicht vor Augen.^pDu aber, Herr, bist ein barmherziger und gndiger Gott,^*du bist langmtig, reich an Huld und Treue.^pWende dich mir zu und sei mir gndig,^+gib deinem Knecht wieder Kraft^*und hilf dem Sohn deiner Magd!^pTu ein Zeichen und schenke mir Glck!^+Alle, die mich hassen, sollen es sehen und sich schmen,^*weil du, Herr, mich gerettet und getrstet hast."
