        "text": "Herr, du Gott meines Heils,^*zu dir schreie ich am Tag und bei Nacht.^pLass mein Gebet zu dir dringen,^*wende dein Ohr meinem Flehen zu!^pDenn meine Seele ist gesttigt mit Leid,^*mein Leben ist dem Totenreich nahe.^pSchon zhle ich zu denen, die hinabsinken ins Grab,^*bin wie ein Mann, dem alle Kraft genommen ist.^pIch bin zu den Toten hinweggerafft,^*wie Erschlagene, die im Grabe ruhen;^pan sie denkst du nicht mehr,^*denn sie sind deiner Hand entzogen.^pDu hast mich ins tiefste Grab gebracht,^*tief hinab in finstere Nacht.^pSchwer lastet dein Grimm auf mir,^*all deine Wogen strzen ber mir zusammen.^pDie Freunde hast du mir entfremdet,^+mich ihrem Abscheu ausgesetzt;^*ich bin gefangen und kann nicht heraus.^pMein Auge wird trbe vor Elend.^+Jeden Tag, Herr, ruf ich zu dir;^*ich strecke nach dir meine Hnde aus.^pWirst du an den Toten Wunder tun,^*werden Schatten aufstehn, um dich zu preisen?^pErzhlt man im Grab von deiner Huld,^*von deiner Treue im Totenreich?^pWerden deine Wunder in der Finsternis bekannt,^*deine Gerechtigkeit im Land des Vergessens?^pHerr, darum schreie ich zu dir,^*frh am Morgen tritt mein Gebet vor dich hin.^pWarum, o Herr, verwirfst du mich,^*warum verbirgst du dein Gesicht vor mir?^pGebeugt bin ich und todkrank von frher Jugend an,^*deine Schrecken lasten auf mir, und ich bin zerqult.^pber mich fuhr die Glut deines Zorns dahin,^*deine Schrecken vernichten mich.^pSie umfluten mich allzeit wie Wasser^*und dringen auf mich ein von allen Seiten.^pDu hast mir die Freunde und Gefhrten entfremdet;^*mein Vertrauter ist nur noch die Finsternis."
