        "text": "Von den Taten deiner Huld, Herr, will ich ewig singen,^*bis zum fernsten Geschlecht laut deine Treue verknden.^pDenn ich bekenne: Deine Huld besteht fr immer und ewig;^*deine Treue steht fest im Himmel.^pIch habe einen Bund geschlossen mit meinem Erwhlten^*und David, meinem Knecht, geschworen:^pDeinem Haus gebe ich auf ewig Bestand^*und von Geschlecht zu Geschlecht richte ich deinen Thron auf.^pDie Himmel preisen, Herr, deine Wunder^*und die Gemeinde der Heiligen deine Treue.^pDenn wer ber den Wolken ist wie der Herr,^*wer von den Gttern ist dem Herrn gleich?^pGewaltig ist Gott im Rat der Heiligen,^*fr alle rings um ihn her ist er gro und furchtbar.^pHerr, Gott der Heerscharen, wer ist wie du?^*Mchtig bist du, Herr, und von Treue umgeben.^pDu beherrschst die Emprung des Meeres;^*wenn seine Wogen toben  du glttest sie.^pRahab hast du durchbohrt und zertreten,^*deine Feinde zerstreut mit starkem Arm.^pDein ist der Himmel, dein auch die Erde;^*den Erdkreis und was ihn erfllt hast du gegrndet.^pNord und Sd hast du geschaffen,^*Tabor und Hermon jauchzen bei deinem Namen.^pDein Arm ist voll Kraft,^*deine Hand ist stark, deine Rechte hoch erhoben.^pRecht und Gerechtigkeit sind die Sttzen deines Thrones,^*Huld und Treue schreiten vor deinem Antlitz her.^pWohl dem Volk, das dich als Knig zu feiern wei!^*Herr, sie gehen im Licht deines Angesichts.^pSie freuen sich ber deinen Namen zu jeder Zeit,^*ber deine Gerechtigkeit jubeln sie.^pDenn du bist ihre Schnheit und Strke,^*du erhhst unsre Kraft in deiner Gte.^pJa, unser Schild gehrt dem Herrn,^*unser Knig dem heiligen Gott Israels."
