        "text": "Einst hast du, Herr, in einer Vision zu deinen Frommen gesprochen:^+Einen Helden habe ich zum Knig gekrnt,^*einen jungen Mann aus dem Volk erhht.^pIch habe David, meinen Knecht, gefunden^*und ihn mit meinem heiligen l gesalbt.^pBestndig wird meine Hand ihn halten^*und mein Arm ihn strken.^pKein Feind soll ihn tuschen,^*kein ruchloser Mensch kann ihn bezwingen.^pVor ihm will ich die Feinde zerschmettern^*und alle, die ihn hassen, schlage ich nieder.^pMeine Treue und meine Huld begleiten ihn^*und in meinem Namen erhebt er sein Haupt.^pIch lege seine Hand auf das Meer,^*ber die Strme herrscht seine Rechte.^pEr wird zu mir rufen: Mein Vater bist du,^*mein Gott, der Fels meines Heiles.^pIch mache ihn zum erstgeborenen Sohn,^*zum Hchsten unter den Herrschern der Erde.^pAuf ewig werde ich ihm meine Huld bewahren,^*mein Bund mit ihm bleibt allzeit bestehen.^pSein Geschlecht lasse ich dauern fr immer^*und seinen Thron, solange der Himmel whrt."
