        "title": "Dank fr Rettung und Heil",
        "quote": "Er ist der Stein, der von euch Bauleuten verworfen wurde, der aber zum Eckstein geworden ist. (Apg 4,11)",
        "text": "Danket dem Herrn, denn er ist gtig,^*denn seine Huld whrt ewig!^pSo soll Israel sagen:^*Denn seine Huld whrt ewig.^pSo soll das Haus Aaron sagen:^*Denn seine Huld whrt ewig.^pSo sollen alle sagen, die den Herrn frchten und ehren:^*Denn seine Huld whrt ewig.^pIn der Bedrngnis rief ich zum Herrn;^*der Herr hat mich erhrt und mich frei gemacht.^pDer Herr ist bei mir, ich frchte mich nicht.^*Was knnen Menschen mir antun?^pDer Herr ist bei mir, er ist mein Helfer;^*ich aber schaue auf meine Hasser herab.^pBesser, sich zu bergen beim Herrn,^*als auf Menschen zu bauen.^pBesser, sich zu bergen beim Herrn,^*als auf Frsten zu bauen."
