        "title": "Der Hter Israels",
        "quote": "Sie werden keinen Hunger und keinen Durst mehr haben, und weder Sonnenglut noch sengende Hitze wird auf ihnen lasten. (Offb 7,16)",
        "text": "Ich hebe meine Augen auf zu den Bergen:^*Woher kommt mir Hilfe?^pMeine Hilfe kommt vom Herrn,^*der Himmel und Erde gemacht hat.^pEr lsst deinen Fu nicht wanken;^*er, der dich behtet, schlft nicht.^pNein, der Hter Israels^*schlft und schlummert nicht.^pDer Herr ist dein Hter, der Herr gibt dir Schatten;^*er steht dir zur Seite.^pBei Tag wird dir die Sonne nicht schaden^*noch der Mond in der Nacht.^pDer Herr behte dich vor allem Bsen,^*er behte dein Leben.^pDer Herr behte dich, wenn du fortgehst und wiederkommst,^*von nun an bis in Ewigkeit."
