        "text": "Ich freute mich, als man mir sagte:^*Zum Haus des Herrn wollen wir pilgern.^pSchon stehen wir in deinen Toren, Jerusalem:^+Jerusalem, du starke Stadt,^*dicht gebaut und fest gefgt.^pDorthin ziehen die Stmme hinauf, die Stmme des Herrn,^+wie es Israel geboten ist,^*den Namen des Herrn zu preisen.^pDenn dort stehen Throne bereit fr das Gericht,^*die Throne des Hauses David.^pErbittet fr Jerusalem Frieden!^*Wer dich liebt, sei in dir geborgen.^pFriede wohne in deinen Mauern,^*in deinen Husern Geborgenheit!^pWegen meiner Brder und Freunde^*will ich sagen: In dir sei Friede!^pWegen des Hauses des Herrn, unseres Gottes,^*will ich dir Glck erflehen."
