        "text": "Htte sich nicht der Herr fr uns eingesetzt^* so soll Israel sagen ,^phtte sich nicht der Herr fr uns eingesetzt,^*als sich gegen uns Menschen erhoben,^pdann htten sie uns lebendig verschlungen,^*als gegen uns ihr Zorn entbrannt war.^pDann htten die Wasser uns weggesplt,^*htte sich ber uns ein Wildbach ergossen.^pDann htten sich ber uns die Wasser ergossen,^*die wilden und wogenden Wasser.^pGelobt sei der Herr,^*der uns nicht ihren Zhnen als Beute berlie.^pUnsre Seele ist wie ein Vogel dem Netz des Jgers entkommen,^*das Netz ist zerrissen, und wir sind frei.^pUnsre Hilfe steht im Namen des Herrn,^*der Himmel und Erde gemacht hat."
