        "text": "Als der Herr das Los der Gefangenschaft Zions wendete,^*da waren wir alle wie Trumende.^pDa war unser Mund voll Lachen^*und unsere Zunge voll Jubel.^pDa sagte man unter den andern Vlkern:^*Der Herr hat an ihnen Groes getan.^pJa, Groes hat der Herr an uns getan.^*Da waren wir frhlich.^pWende doch, Herr, unser Geschick,^*wie du versiegte Bche wieder fllst im Sdland.^pDie mit Trnen sen,^*werden mit Jubel ernten.^pSie gehen hin unter Trnen^*und tragen den Samen zur Aussaat.^pSie kommen wieder mit Jubel^*und bringen ihre Garben ein."
