        "text": "Sie haben mich oft bedrngt von Jugend auf^* so soll Israel sagen ,^psie haben mich oft bedrngt von Jugend auf,^*doch sie konnten mich nicht bezwingen.^pDie Pflger haben auf meinem Rcken gepflgt,^*ihre langen Furchen gezogen.^pDoch der Herr ist gerecht,^*er hat die Stricke der Frevler zerhauen.^pBeschmt sollen alle weichen,^*alle, die Zion hassen.^pSie sollen wie das Gras auf den Dchern sein,^*das verdorrt, noch bevor man es ausreit.^pKein Schnitter kann seine Hand damit fllen,^*kein Garbenbinder den Arm.^pKeiner, der vorbergeht, wird sagen:^+Der Segen des Herrn sei mit euch!  *^lWir aber segnen euch im Namen des Herrn."
