        "text": "Ich will dir danken aus ganzem Herzen,^*dir vor den Engeln singen und spielen;^pich will mich niederwerfen zu deinem heiligen Tempel hin^*und deinem Namen danken fr deine Huld und Treue.^pDenn du hast die Worte meines Mundes gehrt,^*deinen Namen und dein Wort ber alles verherrlicht.^pDu hast mich erhrt an dem Tag, als ich rief;^*du gabst meiner Seele groe Kraft.^pDich sollen preisen, Herr, alle Knige der Welt,^*wenn sie die Worte deines Mundes vernehmen.^pSie sollen singen von den Wegen des Herrn;^*denn gro ist die Herrlichkeit des Herrn.^pJa, der Herr ist erhaben;^+doch er schaut auf die Niedrigen^*und die Stolzen erkennt er von fern.^pGehe ich auch mitten durch groe Not:^*du erhltst mich am Leben.^pDu streckst die Hand aus gegen meine wtenden Feinde,^*und deine Rechte hilft mir.^pDer Herr nimmt sich meiner an.^+Herr, deine Huld whrt ewig.^*Lass nicht ab vom Werk deiner Hnde!"
