        "title": "Der Mensch in Gottes Blick",
        "quote": "Wer hat die Gedanken des Herrn erkannt? Oder wer ist sein Ratgeber gewesen? (Rm 11,34)",
        "text": "Herr, du hast mich erforscht, und du kennst mich.^+Ob ich sitze oder stehe, du weit von mir.^*Von fern erkennst du meine Gedanken.^pOb ich gehe oder ruhe, es ist dir bekannt;^*du bist vertraut mit all meinen Wegen.^pNoch liegt mir das Wort nicht auf der Zunge  *^ldu, Herr, kennst es bereits.^pDu umschliet mich von allen Seiten^*und legst deine Hand auf mich.^pZu wunderbar ist fr mich dieses Wissen,^*zu hoch, ich kann es nicht begreifen.^pWohin knnte ich fliehen vor deinem Geist,^*wohin mich vor deinem Angesicht flchten?^pSteige ich hinauf in den Himmel, so bist du dort;^*bette ich mich in der Unterwelt, bist du zugegen.^pNehme ich die Flgel des Morgenrots^*und lasse mich nieder am uersten Meer,^pauch dort wird deine Hand mich ergreifen^*und deine Rechte mich fassen.^pWrde ich sagen: Finsternis soll mich bedecken,^+statt Licht soll Nacht mich umgeben,^*auch die Finsternis wre fr dich nicht finster,^pdie Nacht wrde leuchten wie der Tag,^*die Finsternis wre wie Licht."
