        "quote": "Jetzt wird der Menschensohn den Sndern ausgeliefert. (Mt 26,45)",
        "text": "Rette mich, Herr, vor bsen Menschen,^*vor gewaltttigen Leuten schtze mich!^pDenn sie sinnen in ihrem Herzen auf Bses,^*jeden Tag schren sie Streit.^pWie die Schlangen haben sie scharfe Zungen^*und hinter den Lippen Gift wie die Nattern.^pBehte mich, Herr, vor den Hnden der Frevler,^+vor gewaltttigen Leuten schtze mich,^*die darauf sinnen, mich zu Boden zu stoen.^pHochmtige legen mir heimlich Schlingen,^+Bse spannen ein Netz aus,^*stellen mir Fallen am Wegrand.^pIch sage zum Herrn: Du bist mein Gott.^*Vernimm, o Herr, mein lautes Flehen!^pHerr, mein Gebieter, meine starke Hilfe,^*du beschirmst mein Haupt am Tag des Kampfes.^pHerr, erflle nicht die Wnsche des Frevlers,^*lass seine Plne nicht gelingen!^pIch wei, der Herr fhrt die Sache des Armen,^*er verhilft den Gebeugten zum Recht.^pDeinen Namen preisen nur die Gerechten;^*vor deinem Angesicht drfen nur die Redlichen bleiben."
