        "title": "Bitte um Bewahrung",
        "quote": "Aus der Hand des Engels stieg der Weihrauch vor Gott empor: das sind die Gebete der Heiligen. (Offb 8,4)",
        "text": "Herr, ich rufe zu dir. Eile mir zu Hilfe;^*hre auf meine Stimme, wenn ich zu dir rufe!^pWie ein Rauchopfer steige mein Gebet vor dir auf;^*als Abendopfer gelte vor dir, wenn ich meine Hnde erhebe.^pHerr, stell eine Wache vor meinen Mund,^*eine Wehr vor das Tor meiner Lippen!^pGib, dass mein Herz sich bsen Worten nicht zuneigt,^*dass ich nichts tue, was schndlich ist,^pzusammen mit Menschen, die Unrecht tun.^*Von ihren Leckerbissen will ich nicht kosten.^pDer Gerechte mag mich schlagen aus Gte:^*wenn er mich bessert, ist es Salbl fr mein Haupt;^pda wird sich mein Haupt nicht struben.^*Ist er in Not, will ich stets fr ihn beten.^pHaben ihre Richter sich auch die Felsen hinabgestrzt,^*sie sollen hren, dass mein Wort fr sie freundlich ist.^pWie wenn man Furchen zieht und das Erdreich aufreit,^*so sind unsre Glieder hingestreut an den Rand der Unterwelt.^pMein Herr und Gott, meine Augen richten sich auf dich;^*bei dir berge ich mich. Gie mein Leben nicht aus!^pVor der Schlinge, die sie mir legten, bewahre mich,^*vor den Fallen derer, die Unrecht tun!"
