        "text": "Herr, hre mein Gebet, vernimm mein Flehen;^*in deiner Treue erhre mich, in deiner Gerechtigkeit!^pGeh mit deinem Knecht nicht ins Gericht;^*denn keiner, der lebt, ist gerecht vor dir.^pDer Feind verfolgt mich, tritt mein Leben zu Boden,^*er lsst mich in der Finsternis wohnen wie lngst Verstorbene.^pMein Geist verzagt in mir,^*mir erstarrt das Herz in der Brust.^pIch denke an die vergangenen Tage,^+sinne nach ber all deine Taten,^*erwge das Werk deiner Hnde.^pIch breite die Hnde aus und bete zu dir;^*meine Seele drstet nach dir wie lechzendes Land.^pHerr, erhre mich bald,^*denn mein Geist wird mde;^pverbirg dein Antlitz nicht vor mir,^*damit ich nicht werde wie Menschen, die lngst begraben sind.^pLass mich deine Huld erfahren am frhen Morgen;^*denn ich vertraue auf dich.^pZeig mir den Weg, den ich gehen soll;^*denn ich erhebe meine Seele zu dir.^pHerr, entrei mich den Feinden!^*Zu dir nehme ich meine Zuflucht.^pLehre mich, deinen Willen zu tun; denn du bist mein Gott.^*Dein guter Geist leite mich auf ebenem Pfad.^pUm deines Namens willen, Herr, erhalt mich am Leben,^*fhre mich heraus aus der Not in deiner Gerechtigkeit!"
