        "quote": "Gerecht bist du, der du bist und der du warst, du Heiliger. (Offb 16,5)",
        "text": "Ich will dich rhmen, mein Gott und Knig,^*und deinen Namen preisen immer und ewig;^pIch will dich preisen Tag fr Tag^*und deinen Namen loben immer und ewig.^pGro ist der Herr und hoch zu loben,^*seine Gre ist unerforschlich.^pEin Geschlecht verknde dem andern den Ruhm deiner Werke^*und erzhle von deinen gewaltigen Taten.^pSie sollen vom herrlichen Glanz deiner Hoheit reden;^*ich will deine Wunder besingen.^pSie sollen sprechen von der Gewalt deiner erschreckenden Taten;^*ich will von deinen groen Taten berichten.^pSie sollen die Erinnerung an deine groe Gte wecken^*und ber deine Gerechtigkeit jubeln.^pDer Herr ist gndig und barmherzig,^*langmtig und reich an Gnade.^pDer Herr ist gtig zu allen,^*sein Erbarmen waltet ber all seinen Werken.^pDanken sollen dir, Herr, all deine Werke^*und deine Frommen dich preisen.^pSie sollen von der Herrlichkeit deines Knigtums reden,^*sollen sprechen von deiner Macht,^pden Menschen deine machtvollen Taten verknden^*und den herrlichen Glanz deines Knigtums.^pDein Knigtum ist ein Knigtum fr ewige Zeiten,^*deine Herrschaft whrt von Geschlecht zu Geschlecht."
