        "quote": "Wir loben den Herrn in unserem Leben, das heit durch unser gutes Handeln. (Arnobius)",
        "text": "Lobe den Herrn, meine Seele!^+Ich will den Herrn loben, solange ich lebe,^*meinem Gott singen und spielen, solange ich da bin.^pVerlasst euch nicht auf Frsten,^*auf Menschen, bei denen es doch keine Hilfe gibt.^pHaucht der Mensch sein Leben aus^+und kehrt er zurck zur Erde,^*dann ist es aus mit all seinen Plnen.^pWohl dem, dessen Halt der Gott Jakobs ist^*und der seine Hoffnung auf den Herrn, seinen Gott, setzt.^pDer Herr hat Himmel und Erde gemacht,^+das Meer und alle Geschpfe;^*er hlt ewig die Treue.^pRecht verschafft er den Unterdrckten,^+den Hungernden gibt er Brot;^*der Herr befreit die Gefangenen.^pDer Herr ffnet den Blinden die Augen,^*er richtet die Gebeugten auf.^pDer Herr beschtzt die Fremden^*und verhilft den Waisen und Witwen zu ihrem Recht.^pDer Herr liebt die Gerechten,^*doch die Schritte der Frevler leitet er in die Irre.^pDer Herr ist Knig auf ewig,^*dein Gott, Zion, herrscht von Geschlecht zu Geschlecht."
