        "quote": "Dich, Gott, loben wir, dich, Herr, preisen wir.",
        "text": "^(Gut ist es, unserem Gott zu singen;^*schn ist es, ihn zu loben.^)^pDer Herr baut Jerusalem wieder auf,^*er sammelt die Versprengten Israels.^pEr heilt die gebrochenen Herzen^*und verbindet ihre schmerzenden Wunden.^pEr bestimmt die Zahl der Sterne^*und ruft sie alle mit Namen.^pGro ist unser Herr und gewaltig an Kraft,^*unermesslich ist seine Weisheit.^pDer Herr hilft den Gebeugten auf^*und erniedrigt die Frevler.^pStimmt dem Herrn ein Danklied an,^*spielt unserem Gott auf der Harfe!^pEr bedeckt den Himmel mit Wolken,^+spendet der Erde Regen^*und lsst Gras auf den Bergen sprieen.^pEr gibt dem Vieh seine Nahrung,^*gibt den jungen Raben, wonach sie schreien.^pEr hat keine Freude an der Kraft des Pferdes,^*kein Gefallen am schnellen Lauf des Mannes.^pGefallen hat der Herr an denen, die ihn frchten und ehren,^*die voll Vertrauen warten auf seine Huld."
