        "text": "Lobet den Herrn vom Himmel her,^*lobt ihn in den Hhen:^pLobt ihn, all seine Engel,^*lobt ihn, all seine Scharen;^plobt ihn, Sonne und Mond,^*lobt ihn, all ihr leuchtenden Sterne;^plobt ihn, alle Himmel^*und ihr Wasser ber dem Himmel!^pLoben sollen sie den Namen des Herrn;^*denn er gebot, und sie waren erschaffen.^pEr stellte sie hin fr immer und ewig,^*er gab ihnen ein Gesetz, das sie nicht bertreten.^pLobt den Herrn, ihr auf der Erde;^*ihr Seeungeheuer und all ihr Tiefen,^pFeuer und Hagel, Schnee und Nebel,^*du Sturmwind, der sein Wort vollzieht;^pihr Berge und all ihr Hgel,^*ihr Fruchtbume und alle Zedern;^pihr wilden Tiere und alles Vieh,^*Kriechtiere und gefiederte Vgel;^pihr Knige der Erde und alle Vlker,^*ihr Frsten und alle Richter auf Erden;^pihr jungen Mnner und auch ihr Mdchen,^*ihr Alten mit den Jungen.^pLoben sollen sie den Namen des Herrn;^+denn sein Name allein ist erhaben,^*seine Hoheit strahlt ber Erde und Himmel.^pSeinem Volk verleiht er Macht,^+das ist ein Ruhm fr all seine Frommen,^*fr Israels Kinder, das Volk, das ihm nahen darf."
