        "text": "Ich sagte: In der Mitte meiner Tage^+muss ich hinab zu den Pforten der Unterwelt,^*man raubt mir den Rest meiner Jahre.^pIch darf den Herrn nicht mehr schauen im Land der Lebenden,^*keinen Menschen mehr sehen bei den Bewohnern der Erde.^pMeine Htte bricht man ber mir ab,^*man schafft sie weg wie das Zelt eines Hirten.^pWie ein Weber hast du mein Leben zu Ende gewoben,^*du schneidest mich ab wie ein fertig gewobenes Tuch.^pVom Anbruch des Tages bis in die Nacht gibst du mich preis;^*bis zum Morgen schreie ich um Hilfe.^pMeine Augen blicken ermattet nach oben:^*Ich bin in Not, Herr; steh mir bei!^pDu hast mich aus meiner bitteren Not gerettet,^+du hast mich vor dem tdlichen Abgrund bewahrt;^*denn all meine Snden warfst du hinter deinen Rcken.^pJa, in der Unterwelt dankt man dir nicht,^+die Toten loben dich nicht;^*wer ins Grab gesunken ist, kann nichts mehr von deiner Gte erhoffen.^pNur die Lebenden danken dir, wie ich am heutigen Tag.^*Von deiner Treue erzhlt der Vater den Kindern.^pDer Herr war bereit, mir zu helfen.^*Wir wollen singen und spielen im Haus des Herrn, solange wir leben!"
