        "text": "Mein Herz ist voll Freude ber den Herrn,^*groe Kraft gibt mir der Herr.^pWeit ffnet sich mein Mund gegen meine Feinde,^*denn ich freue mich ber deine Hilfe.^pNiemand ist heilig, nur der Herr,^+denn auer dir gibt es keinen Gott;^*keiner ist ein Fels wie unser Gott.^pRedet nicht immer so vermessen,^*kein freches Wort komme aus eurem Mund;^pdenn der Herr ist ein wissender Gott,^*und bei ihm werden die Taten geprft.^pDer Bogen der Helden wird zerbrochen,^*die Wankenden aber grten sich mit Kraft.^pDie Satten verdingen sich um Brot,^*doch die Hungrigen knnen feiern fr immer.^pDie Unfruchtbare bekommt sieben Kinder,^*doch die Kinderreiche welkt dahin.^pDer Herr macht tot und lebendig,^*er fhrt zum Totenreich hinab und fhrt auch herauf.^pDer Herr macht arm und macht reich,^*er erniedrigt, und er erhht.^pDen Schwachen hebt er empor aus dem Staub^*und erhht den Armen, der im Schmutz liegt;^per gibt ihm einen Sitz bei den Edlen,^*einen Ehrenplatz weist er ihm zu.^pJa, dem Herrn gehren die Pfeiler der Erde;^*auf sie hat er den Erdkreis gegrndet.^pEr behtet die Schritte seiner Frommen,^+doch die Frevler verstummen in der Finsternis;^*denn der Mensch ist nicht stark aus eigener Kraft.^pWer gegen den Herrn streitet, wird zerbrechen;^+der Hchste lsst es donnern am Himmel.^*Der Herr hlt Gericht bis an die Grenzen der Erde.^pSeinem Knig gebe er Kraft^*und erhhe die Macht seines Gesalbten."
