        "text": "Hrt zu, ihr Himmel, ich will reden,^*die Erde lausche meinen Worten!^pMeine Lehre wird strmen wie Regen,^*meine Botschaft wird fallen wie Tau,^pwie Regentropfen auf das Gras^*und wie Tauperlen auf die Pflanzen.^pIch will den Namen des Herrn verknden;^*preist die Gre unseres Gottes!^pEr heit: Der Fels.^+Vollkommen ist, was er tut;^*denn alle seine Wege sind recht.^pEr ist ein unbeirrbar treuer Gott,^*er ist gerecht und gerade.^pEin falsches, verdrehtes Geschlecht fiel von ihm ab,^*Verkrppelte, die nicht mehr seine Shne sind.^pIst das euer Dank an den Herrn,^*du dummes, verblendetes Volk?^pIst er nicht dein Vater, dein Schpfer?^*Hat er dich nicht geformt und hingestellt?^pDenk an die Tage der Vergangenheit,^*lerne aus den Jahren der Geschichte.^pFrag deinen Vater, er wird es dir erzhlen,^*frag die Alten, sie werden es dir sagen.^pDer Herr nahm sich sein Volk als Anteil,^*Jakob wurde sein Erbland.^pEr fand ihn in der Steppe,^*in der Wste, wo wildes Getier heult.^pEr hllte ihn ein, gab auf ihn acht^*und htete ihn wie seinen Augenstern,^pwie der Adler, der sein Nest beschtzt^*und ber seinen Jungen schwebt,^pder seine Schwingen ausbreitet, ein Junges ergreift^*und es flgelschlagend davontrgt.^pDer Herr allein hat Jakob geleitet,^*kein fremder Gott stand ihm zur Seite."
