        "text": "Seht, Gott der Herr, kommt mit Macht,^*er herrscht mit starkem Arm.^pSeht, er bringt seinen Siegespreis mit:^*Alle, die er gewonnen hat, gehen vor ihm her.^pWie ein Hirt fhrt er seine Herde zur Weide,^*er sammelt sie mit starker Hand.^pDie Lmmer trgt er auf dem Arm,^*die Mutterschafe fhrt er behutsam.^pWer misst das Meer mit der hohlen Hand?^*Wer kann mit der ausgespannten Hand den Himmel vermessen?^pWer misst den Staub der Erde mit einem Scheffel?^+Wer wiegt die Berge mit einer Waage^*und mit Gewichten die Hgel?^pWer bestimmt den Geist des Herrn?^*Wer kann sein Berater sein und ihn unterrichten?^pWen fragt er um Rat,^*und wer vermittelt ihm Einsicht?^pWer kann ihn ber die Pfade des Rechts belehren?^*Wer lehrt ihn das Wissen und zeigt ihm den Weg der Erkenntnis?^pSeht, die Vlker sind wie ein Tropfen am Eimer,^+sie gelten so viel wie ein Stubchen auf der Waage.^*Ganze Inseln wiegen nicht mehr als ein Sandkorn.^pDer Libanon reicht nicht aus fr das Brennholz,^*sein Wild gengt nicht fr die Opfer.^pAlle Vlker sind vor Gott wie ein Nichts,^*fr ihn sind sie wertlos und nichtig."
