        "text": "Gott der Vter und Herr des Erbarmens,^*du hast das All durch dein Wort gemacht.^pDen Menschen hast du durch deine Weisheit erschaffen,^*damit er ber deine Geschpfe herrsche.^pEr soll die Welt in Heiligkeit und Gerechtigkeit leiten^*und Gericht halten in rechter Gesinnung.^pGib mir die Weisheit, die an deiner Seite thront,^*und versto mich nicht aus der Schar deiner Kinder!^pIch bin ja dein Knecht, der Sohn deiner Magd,^+ein schwacher Mensch, dessen Leben nur kurz ist,^*und gering ist meine Einsicht in Recht und Gesetz.^pWre einer auch vollkommen unter den Menschen,^*er wird kein Ansehen genieen, wenn ihm deine Weisheit fehlt.^pMit dir ist die Weisheit, die deine Werke kennt^*und die zugegen war, als du die Welt erschufst.^pSie wei, was dir gefllt^*und was recht ist nach deinen Geboten.^pSende sie vom heiligen Himmel^*und schick sie vom Thron deiner Herrlichkeit,^pdamit sie bei mir sei und alle Mhe mit mir teile,^*und damit ich erkenne, was dir gefllt!^pDenn sie wei und versteht alles;^+sie wird mich in meinem Tun besonnen leiten^*und mich in ihrem Lichtglanz schtzen."
