        "text": "Gepriesen und gelobt bist du, Herr, Gott unsrer Vter;^*herrlich ist dein Name in alle Ewigkeit.^pDenn du bist gerecht in allem, was du getan hast.^+All deine Taten sind richtig, deine Wege gerade.^*Alle deine Urteile sind wahr.^pDenn wir haben gesndigt und durch Treubruch gefrevelt^*und haben in allem gefehlt.^pUm deines Namens willen verwirf uns nicht fr immer;^*lse deinen Bund nicht auf!^pVersag uns nicht dein Erbarmen,^+deinem Freund Abraham zuliebe, deinem Knechte Isaak^*und Israel, deinem Heiligen,^pdenen du Nachkommen verheien hast,^+so zahlreich wie die Sterne am Himmel^*und wie der Sand am Ufer des Meeres.^pAch, Herr, wir sind geringer geworden als alle Vlker.^*In aller Welt sind wir heute wegen unserer Snden erniedrigt.^pWir haben in dieser Zeit weder Vorsteher noch Propheten^*und keinen, der uns anfhrt,^pweder Brandopfer noch Schlachtopfer, weder Speiseopfer noch Rucherwerk,^*noch einen Ort, um dir die Erstlingsgaben darzubringen und um Erbarmen zu finden bei dir.^pDu aber nimm uns an.^*Wir kommen mit zerknirschtem Herzen und demtigem Sinn.^pWie Brandopfer von Widdern und Stieren,^+wie Tausende fetter Lmmer,^*so gelte heute unser Opfer vor dir und verschaffe uns bei dir Shne.^pDenn wer dir vertraut, wird nicht beschmt.+^pWir folgen dir jetzt von ganzem Herzen,^*frchten dich und suchen dein Angesicht."
