        "text": "Von Herzen will ich mich freuen ber den Herrn.^*Meine Seele soll jubeln ber meinen Gott.^pDenn er kleidet mich in Gewnder des Heils,^*er hllt mich in den Mantel der Gerechtigkeit,^pwie ein Brutigam sich festlich schmckt^*und wie eine Braut ihr Geschmeide anlegt.^pDenn wie die Erde die Saat wachsen lsst^*und der Garten die Pflanzen hervorbringt,^pso bringt Gott, der Herr, Gerechtigkeit hervor^*und Ruhm vor allen Vlkern.^pUm Zions willen kann ich nicht schweigen^*und um Jerusalems willen nicht still sein,^pbis das Recht in ihm aufstrahlt wie ein helles Licht^*und sein Heil aufleuchtet wie eine brennende Fackel.^pMan ruft dich mit einem neuen Namen,^*den der Mund des Herrn fr dich bestimmt.^pDu wirst zu einer prchtigen Krone in der Hand des Herrn,^*zu einem kniglichen Diadem in der Rechten deines Gottes.^pNicht lnger nennt man dich Die Verlassene^*und dein Land nicht mehr Das dland,^psondern man nennt dich Meine Wonne^*und dein Land Die Vermhlte.^pDenn der Herr hat an dir seine Freude,^*und dein Land wird mit ihm vermhlt.^pWie der junge Mann sich mit der Jungfrau vermhlt,^*so vermhlt sich mit dir dein Erbauer.^pWie der Brutigam sich freut ber die Braut,^*so freut sich dein Gott ber dich."
