        "text": "Gepriesen sei der Herr, der Gott Israels!^*Denn er hat sein Volk besucht und ihm Erlsung geschaffen;^per hat uns einen starken Retter erweckt^*im Hause seines Knechtes David.^pSo hat er verheien von alters her^*durch den Mund seiner heiligen Propheten.^pEr hat uns errettet vor unsern Feinden^*und aus der Hand aller, die uns hassen;^per hat das Erbarmen mit den Vtern an uns vollendet^+und an seinen heiligen Bund gedacht,^*an den Eid, den er unserm Vater Abraham geschworen hat;^per hat uns geschenkt, dass wir, aus Feindeshand befreit,^+ihm furchtlos dienen in Heiligkeit und Gerechtigkeit^*vor seinem Angesicht all unsre Tage.^pUnd du, Kind, wirst Prophet des Hchsten heien;^+denn du wirst dem Herrn vorangehn^*und ihm den Weg bereiten.^pDu wirst sein Volk mit der Erfahrung des Heils beschenken^*in der Vergebung der Snden.^pDurch die barmherzige Liebe unseres Gottes^*wird uns besuchen das aufstrahlende Licht aus der Hhe,^pum allen zu leuchten, die in Finsternis sitzen und im Schatten des Todes,^*und unsre Schritte zu lenken auf den Weg des Friedens."
