        "quote": "1. Vesper zum Christknigssonntag",
        "text": "Christus, du Herrscher Himmels und der Erde,^lHerr ber Mchte, Throne und Gewalten.^lDu bist der Erste, und du bist der Letzte,^lAnfang und Ende.^pIn deinen Hnden ruht der Menschen Schicksal.^lNichts kann auf Erden deiner Macht entgleiten.^lDu sprichst das Urteil ber alle Vlker,^lvoll des Erbarmens.^pReiche erstehen, blhen und zerfallen,^laber das deine berdauert alle,^ldenn deine Herrschaft ist von Gott verliehen,^lewigen Urspungs.^pKeiner der Groen kann mit dir sich messen;^lHerrscher der Herren, Knig aller Zeiten,^lAbglanz des Vaters, Spiegel seiner Hoheit,^lthronend im Himmel.^pDir sei die Ehre, dir und deinem Vater,^lund auch dem Geiste sei das Lob gesungen.^lGott, dem Dreieinen, Lob und Preis und Ehre^limmer und ewig. Amen."
