        "text": "Das erfragte ich unter den Menschen^lals gewaltigstes Wunder:^lAls Erde nicht war noch hoher Himmel,^lnoch Baum noch Berg nicht war,^lnoch Sonne nicht schien noch Stern,^lnoch Mond nicht leuchtete^lnoch das mchtige Meer,^lals nirgends nichts war aller Enden und Wenden:^lda war der eine allmchtige Gott,^lder Herren mildester;^lbei ihm viele Geister voll Herrlichkeit.^lDoch eher als sie war der heilige Gott.^pAllmchtiger Gott,^lder du Himmel und Erde geschaffen^lund den Menschen viel Gutes gegeben hast,^lverleihe mir in deiner Huld den rechten Glauben,^lgewhre mir Weisheit und Klugheit und Kraft,^ldem Verderber zu widerstehn,^ldas Bse zu meiden^lund deinen Willen zu vollbringen. Amen."
