  405: {
    0.0: {
        "quote": "Lh Mo^+Do Nacht",
        "text": "Du Gott des Lichts, dem Vater gleich,^ldu Licht, dem unser Licht entspringt,^ldu ewger Tag: hr unser Flehn,^ldas aus der Nacht zum Himmel dringt.^pEntreie uns der Finsternis^lund aller Angst der Erdennacht,^lstreif ab von uns die Mdigkeit,^ldie uns zum Guten trge macht.^pDu, Christus, bist das Licht der Welt,^lder Gott, dem glubig wir vertraun,^lauf den im Dunkel dieser Zeit^lwir alle unsre Hoffnung baun.^pAus ganzem Herzen preisen wir^ldich, Christus, Herr der Herrlichkeit,^lder mit dem Vater und dem Geist^luns liebt in alle Ewigkeit. Amen."
      }
  },
  406: {
    0.0: {
        "text": "Dich, Gott, loben wir, dich, Herr, preisen wir.^lDir, dem ewigen Vater, huldigt das Erdenrund.^lDir rufen die Engel alle,^ldir Himmel und Mchte insgesamt,^ldie Kerubim dir und die Serafim^lmit niemals endender Stimme zu:^lHeilig, heilig, heilig^lder Herr, der Gott der Scharen!^lVoll sind Himmel und Erde^lvon deiner hohen Herrlichkeit.^pDich preist der glorreiche Chor der Apostel;^ldich der Propheten lobwrdige Zahl;^ldich der Mrtyrer leuchtendes Heer;^ldich preist ber das Erdenrund^ldie heilige Kirche;^ldich, den Vater unermebarer Majestt;^ldeinen wahren und einzigen Sohn;^lund den Heiligen Frsprecher Geist.^pDu Knig der Herrlichkeit, Christus.^lDu bist des Vaters allewiger Sohn.^lDu hast der Jungfrau Scho nicht verschmht,^lbist Mensch geworden,^lden Menschen zu befreien.^lDu hast bezwungen des Todes Stachel^lund denen, die glauben,^ldie Reiche der Himmel aufgetan.^pDu sitzest zur Rechten Gottes^lin deines Vaters Herrlichkeit.^lAls Richter, so glauben wir,^lkehrst du einst wieder.^lDich bitten wir denn,^lkomm deinen Dienern zu Hilfe,^ldie du erlst mit kostbarem Blut.^lIn der ewigen Herrlichkeit^lzhle uns deinen Heiligen zu.^p(Rette dein Volk, o Herr, und segne dein Erbe;^lund fhre sie und erhebe sie bis in Ewigkeit.^lAn jedem Tag benedeien wir dich^lund loben in Ewigkeit deinen Namen,^lja, in der ewigen Ewigkeit.^lIn Gnaden wollest du, Herr,^lan diesem Tag uns ohne Schuld bewahren.^pErbarme dich unser, o Herr, erbarme dich unser.^lLass ber uns dein Erbarmen geschehn,^lwie wir gehofft auf dich.^lAuf dich, o Herr,^lhabe ich meine Hoffnung gesetzt.^lIn Ewigkeit werde ich nicht zuschanden.)"
      }
  },
