          "patr_autor": "II. Vatikanisches Konzil (19621965)",
          "patr_werk": "Aus der dogmatischen Konstitution ber die Kirche.",
          "patr_text": "Wie nmlich der Sohn vom Vater gesandt ist, so hat er selbst die Apostel gesandt (vgl. Joh 20,21) mit den Worten: Gehet hin und lehret alle Vlker, taufet sie im Namen des Vaters und des Sohnes und des Heiligen Geistes, lehret sie alles halten, was ich euch geboten habe. Und siehe, ich bin bei euch alle Tage bis ans Ende der Welt (Mt 28,1820). Diesen feierlichen Auftrag Christi zur Verkndigung der Heilswahrheit hat die Kirche von den Aposteln erhalten und muss ihn erfllen bis zu den Grenzen der Erde (vgl. Apg 1,8).^l^lDaher macht sie sich die Worte des Apostels zu eigen: Weh mir, wenn ich die Frohbotschaft nicht verknde! (1 Kor 9,16) Unablssig fhrt sie darum fort, Verknder auszusenden, bis die neuen Kirchen voll errichtet sind und auch selbst das Werk der Verkndigung fortsetzen knnen. Sie wird nmlich vom Heiligen Geiste angetrieben, mitzuwirken, dass der Ratschluss Gottes, der Christus zum Ursprung des Heils fr die ganze Welt bestellt hat, tatschlich ausgefhrt werde. In der Verkndigung der Frohbotschaft sucht die Kirche die Hrer zum Glauben und zum Bekenntnis des Glaubens zu bringen, bereitet sie fr die Taufe Vor, befreit sie aus der Knechtschaft des Irrtums und gliedert sie Christus ein, damit sie durch die Liebe bis zur Flle in ihn hineinwachsen.^l^lIhre Mhe aber bewirkt, dass aller Same des Guten, der sich in Herz und Geist der Menschen oder in den eigenen Riten und Kulturen der Vlker findet, nicht nur nicht untergehe, sondern geheilt, erhoben und vollendet werde zur Ehre Gottes, zur Beschmung des Teufels und zur Seligkeit des Menschen.",
          "patrresp_1": "Gebt acht auf die ganze Herde, in der euch der Heilige Geist zu Bischfen bestellt hat;",
          "patrresp_2": "sorgt wie Hirten fr die Kirche Gottes, die er sich durch das Blut seines eigenen Sohnes erworben hat.",
