          "oration": "Allmchtiger Gott, du hast den heiligen Valentin befhigt, als Missionar und Bischof den Menschen seiner Zeit ein treuer Helfer zu sein. Wecke in uns das Bewusstsein, dass wir freinander verantwortlich sind, und hilf uns, einander im Glauben zu sttzen. Darum bitten wir durch Jesus Christus."
