          "patr_autor": "Raimund von Penyafort ( 1275)",
          "patr_werk": "Aus einem Brief ber die christliche Lebensfhrung.",
          "patr_titel": "Der Gott des Friedens gebe euren Herzen den Frieden",
          "patr_text": "Wenn der Prediger der Wahrheit mit Recht gesagt hat, dass alle, die fromm in Christus leben, Verfolgung leiden mssen, so meine ich, dass auch in unserer Zeit davon keiner ausgenommen ist, es sei denn, er lege keinen Wert darauf, besonnen, gerecht und fromm in dieser Welt zu leben (Tit 2,12), oder er wisse nichts davon. Ihr sollt auf jeden Fall zu denen gehren, deren Haus ruhig, friedlich und sicher ist, nicht zu denen, die die Zuchtrute Gottes nicht kennen, die ein bequemes Leben fhren und pltzlich zur Hlle fahren.^l^lWeil ihr Gott gefallt und Gnade gefunden habt, so verdient und verlangt es eure Lauterkeit und Frmmigkeit, dass ihr durch hufige Leiden bis zur vollen Reinheit gelutert werdet. Wenn das Schwert zuweilen doppelt und dreifach ber euch kommt, so sollt ihr darin reine Freude und ein Zeichen der Liebe erblicken.^l^lEin zweischneidiges Schwert sind die ueren Kmpfe und die innere Furcht. Es wird im Inneren verdoppelt oder verdreifacht, wenn der verschlagene Feind das Herz mit List und Schmhung beunruhigt. Ihr habt diese Kampfweise bis heute hinreichend erfahren. Sonst wre es unmglich gewesen, zu Frieden und innerer Ruhe mit ihrer Schnheit zu gelangen.^l^luerlich wird das Schwert verdoppelt und verdreifacht, wenn ohne Ursache eine kirchliche Verfolgung wegen geistlicher Fragen ausbricht. Die Wunden sind dann besonders schwer, wenn sie von Freunden kommen.^l^lDarin besteht das ersehnte und selige Kreuz Christi, das Andreas mit wahrhaft mnnlicher Tapferkeit freudigen Herzens annahm, das Kreuz, der einzige Ruhm des auserwhlten Werkzeugs (vgl. Apg 9,15).^l^lSchaut also auf Jesus, den Urheber und Beschtzer des Glaubens, der unschuldig von den Seinen leiden musste und unter die Verbrecher gerechnet wurde. Wenn ihr den Kelch des Herrn Jesus  wie herrlich ist er! (vgl. Ps 23,5 Vg.)  trinkt, sagt dem Herrn Dank, von dem alles Gute kommt.^l^lDer Gott der Liebe und des Friedens gebe euren Herzen den Frieden. Er beschleunige eure Schritte. Er hole euch in die Geborgenheit seines Angesichts, bis er euch in jene Flle (des Lebens) versetzt, wo ihr auf ewig in der Schnheit des Friedens, in den Gezeiten der Zuversicht, in Reichtum und Ruhe wohnen werdet.",
          "patrresp_1": "Mit den Strahlen seiner Lehre erleuchtet er jene, die in Finsternis sitzen.",
          "patrresp_2": "Mit der Glut der Liebe befreit er jene, die in Armut und Eisen gebunden sind.",
