          "oration": "Barmherziger Gott, du hast dem heiligen Raimund die Kraft deiner Liebe geschenkt und ihn den Gefangenen als Helfer gesandt. Hre auf seine Frsprache und befreie uns von der Gefangenschaft der Snde, damit wir als wahrhaft freie Menschen das Gebot der Liebe erfllen. Darum bitten wir durch Jesus Christus."
