    "26": {
      "n1": {
        "name": "",
        "genitiv": "Genitiv",
        "Lesehore": {
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "patr_autor": "Liudger ( 809)",
          "patr_werk": "Aus seiner Lebensbeschreibung des heiligen Gregor von Utrecht.",
          "patr_titel": "Schaut auf das Ende ihres Lebens und ahmt ihren Glauben nach",
          "patr_text": "Die Gebote der Heiligen Schriften mahnen uns den Herrn aus ganzem Herzen und den Nchsten wie uns selbst zu lieben. Sie mahnen uns weiter, dass wir in Wahrheit jene als unsere Nchsten ansehen, die mehr auf das Geistliche als auf das Irdische bedacht sind und sich bis zum letzten Tag durch gute Werke auf die ewigen Verheiungen vorbereiten; diese rhmen sich nicht stolz ihrer hohen Geburt, sondern die Wiedergeburt aus dem Geist bringt ihnen Frucht und Fortschritt.^l^lZwar ist diese Liebe allen Brdern des katholischen Glaubens, der durch die Liebe wirksam ist, zu erweisen, besonders aber unseren heiligen Vtern und Vorgesetzten, wie der heilige Apostel mahnt: Wir bitten euch, Brder: Erkennt die unter euch an, die sich solche Mhe geben, euch im Namen des Herrn zu leiten und zum Rechten anzuhalten. Haltet Frieden untereinander! (1 Thess 5,12f)^l^lDiese Verehrung, die wir unseren Vorgesetzten mehr als anderen erweisen sollen, besteht darin, dass wir ihnen bei Lebzeiten mit lauterem Herzen die schuldige Ehre und Gehorsam erweisen; und nach ihrem Tod sollen wir ihr frommes und segensreiches Vorbild nicht nur in unserem Gedchtnis behalten, sondern wir sollen auch zur Erbauung anderer immer wieder darber reden und es darstellen. So erfllen wir in Wahrheit die Mahnung, die der Apostel an einer anderen Stelle bezglich unserer geistlichen Vter ausspricht: Schaut auf das Ende ihres Lebens und ahmt ihren Glauben nach! (Hebr 13,7b)^l^lWenn wir so im gegenwrtigen Leben ihren Spuren folgen und ihr Vorbild uns getreu zu Herzen nehmen und in unserem Tun beachten, werden wir einst nach dem Wort des Evangeliums mit ihnen in die ewige Freude unseres Herrn eingehen drfen.^l^lWenn wir diese erlangt haben und durch die unverdiente Gnade Christi in die ewige Seligkeit eingegangen sind, wird sie uns in Ewigkeit niemand mehr nehmen; denn dazu sind wir ja im Anfang durch den liebevollen Plan des Schpfers erschaffen und nach dem todbringenden Fall des ersten Menschen durch die erbarmungsvolle Heimsuchung unseres Erlsers von neuem berufen worden. Dahin werden wir am Tag unseres Todes gelangen, wenn wir in den Tagen dieses Lebens mit sicherer Hoffnung die Mahnungen der geistlichen Vter befolgen und wenn Gott selbst immer der hchste Grund unserer Freude ist.^l^lWie gesagt, mssen wir also nach dem klaren Zeugnis der Heiligen Schrift den geistlichen Vtern mit Recht Ehre erweisen und drfen auer den Geboten des Herrn ihren Mahnungen und Beispielen nichts vorziehen. In ihnen haben wir die beste Norm fr unser Leben.",
          "patrresp_1": "Mit aller Klugheit und Bescheidenheit versah Liudger den Dienst des Evangeliums fr die ihm anvertraute Herde,",
          "patrresp_2": "bis er sie mit der Gnade des Herrn zum vollen Glauben gefhrt hatte.",
          "patrresp_3": "Er schaute nicht auf sich selbst, sondern machte sich fr alle zum Diener.",
          "oration": "Allmchtiger Gott, du hast den heiligen Bischof Liudger gesandt, die Friesen und die Sachsen zum christlichen Glauben zu fhren. Gib auch uns unermdlichen Eifer und jene Zuversicht, mit der er sich in den Dienst der Kirche gestellt hat. Darum bitten wir durch Jesus Christus."
        },
        "Laudes": {
          "genitiv": "Genitiv",
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "Ant_Komm": "Nicht ihr werdet reden,^lsondern der Geist eures Vaters redet durch euch.",
          "oration": "Allmchtiger Gott, du hast den heiligen Bischof Liudger gesandt, die Friesen und die Sachsen zum christlichen Glauben zu fhren. Gib auch uns unermdlichen Eifer und jene Zuversicht, mit der er sich in den Dienst der Kirche gestellt hat. Darum bitten wir durch Jesus Christus."
        },
        "Vesper": {
          "genitiv": "Genitiv",
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "Ant_Komm": "Dies ist der treue und kluge Verwalter:^lDer Herr hat ihn eingesetzt, damit er^lseinen Dienern Nahrung gibt zur rechten Zeit.",
          "oration": "Allmchtiger Gott, du hast den heiligen Bischof Liudger gesandt, die Friesen und die Sachsen zum christlichen Glauben zu fhren. Gib auch uns unermdlichen Eifer und jene Zuversicht, mit der er sich in den Dienst der Kirche gestellt hat. Darum bitten wir durch Jesus Christus."
        }
      }
    }
