      "n3": {
        "name": "Cyrill von Alexandrien",
        "genitiv": "Genitiv",
        "Lesehore": {
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "patr_autor": "Cyrill von Alexandrien ( 444)",
          "patr_werk": "Aus einem Brief gegen die Arianer.",
          "patr_titel": "Der Verteidiger der wahren Gottesmutterschaft Marias",
          "patr_text": "Ich wundere mich, dass es Christen gibt, die in Zweifel ziehen, dass Maria Gottesgebrerin genannt werden darf. Denn wenn unser Herr Jesus Christus Gott ist, wie sollte dann die heilige Jungfrau, die ihn geboren hat, nicht Gottesgebrerin sein? Diesen Glauben haben uns die ehrwrdigen Vter berliefert, auch wenn sie den Ausdruck nicht gebrauchten. Die heiligen Vter haben uns angeleitet, so zu denken.^l^lAuch unser Vater Athanasius rhmlichen Andenkens, der den Bischofssitz von Alexandrien ganze 46 Jahre zierte, der den Erfindungen der gottlosen Irrlehrer eine unbesiegliche Einsicht entgegensetzte und mit seinen eigenen Schriften die Welt unter dem Himmel wie mit wohlriechendem Myron erfreute, dem alle die Richtigkeit und Genauigkeit der Lehre bezeugen, nennt im dritten Teil seines Buches ber die wesensgleiche Dreifaltigkeit die heilige Jungfrau immer wieder Gottesgebrerin. Ich sehe mich veranlasst, hier seine eigenen Worte und Ausdrcke zu gebrauchen: Wie wir oft gesagt haben, ist es das Ziel und die Eigenart der Heiligen Schrift, dass ihre Verkndigung ber den Heiland zwei Seiten hat: dass er immer Gott war und der Sohn ist, der Abglanz und die Weisheit des Vaters (vgl. Hebr 1,3), und dass er dann um unseretwillen aus der Jungfrau, der Gottesgebrerin Maria, Fleisch und Menschenwrde annahm. Nach weiteren Ausfhrungen fhrt Athanasius fort: Es hat viele Heilige gegeben, die von aller Snde frei waren: Jeremia wurde schon im Mutterscho geheiligt (vgl. Jer 1,5), und Johannes hpfte vor seiner Geburt jubelnd auf, als er die Stimme Marias hrte (vgl. Lk 1,41). Dieser Mann ist vertrauenswrdig: er wrde nichts sagen, was mit der Heiligen Schrift nicht im Einklang steht.^l^lEs steht also fest, dass der Immanuel aus zwei Wirklichkeiten besteht: aus Gottheit und Menschheit. Dennoch ist der Herr Jesus einer, der eine wahre und natrliche Sohn: Gott und Mensch zugleich, nicht ein vergttlichter Mensch wie die, welche die Gnade besitzen, sondern wahrer Gott, der um unseretwillen in Menschengestalt erschienen ist. Das bezeugt uns auch der heilige Paulus: Als die Zeit erfllt war, sandte Gott seinen Sohn, geboren von einer Frau und dem Gesetz unterstellt, damit er die freikaufte, die unter dem Gesetz stehen, und damit wir die Sohnschaft erlangten (Gal 4,4.5).",
          "patrresp_1": "Er hat vor Gott groe Taten vollbracht, die ganze Erde wurde von seiner Lehre erfllt.",
          "patrresp_2": "Er trete fr uns ein bei Gott, unserm Herrn.",
          "patrresp_3": "Er ist Priester des Herrn und sinnt nach ber Gottes Gebote Tag und Nacht.",
          "oration": "Vater unseres Herrn Jesus Christus, durch deine Gnade ist der heilige Bischof Cyrill von Alexandrien unerschrocken eingetreten fr den Glauben, dass Maria deinen ewigen Sohn geboren hat. Auch wir bekennen sie als wahre Gottesmutter und bitten dich: Rette uns durch die Menschwerdung deines Sohnes Jesus Christus, der in der Einheit des Heiligen Geistes mit dir lebt und herrscht in alle Ewigkeit."
        },
        "Laudes": {
          "name": "Cyrill von Alexandrien",
          "genitiv": "Genitiv",
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "oration": "Vater unseres Herrn Jesus Christus, durch deine Gnade ist der heilige Bischof Cyrill von Alexandrien unerschrocken eingetreten fr den Glauben, dass Maria deinen ewigen Sohn geboren hat. Auch wir bekennen sie als wahre Gottesmutter und bitten dich: Rette uns durch die Menschwerdung deines Sohnes Jesus Christus, der in der Einheit des Heiligen Geistes mit dir lebt und herrscht in alle Ewigkeit."
        },
        "Vesper": {
          "name": "Cyrill von Alexandrien",
          "genitiv": "Genitiv",
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "oration": "Vater unseres Herrn Jesus Christus, durch deine Gnade ist der heilige Bischof Cyrill von Alexandrien unerschrocken eingetreten fr den Glauben, dass Maria deinen ewigen Sohn geboren hat. Auch wir bekennen sie als wahre Gottesmutter und bitten dich: Rette uns durch die Menschwerdung deines Sohnes Jesus Christus, der in der Einheit des Heiligen Geistes mit dir lebt und herrscht in alle Ewigkeit."
        }
      }
