          "oration": "Allmchtiger und barmherziger Gott, durch die Missionsarbeit des heiligen Kilian und seiner Gefhrten hast du im frnkischen Land den christlichen Glauben grundgelegt. Auf die Frsprache dieser Glaubensboten festige uns alle in der Treue zu unserem Herrn Jesus Christus, der in der Einheit des Heiligen Geistes mit dir lebt und herrscht in alle Ewigkeit."
