          "oration": "Barmherziger Gott, du hast die heilige Radegunde aus dem Glanz und der Bedrngnis der Welt in die Stille der Klosterzelle und in deinen Frieden gefhrt. Hilf uns, dass wir unsere Hoffnung nicht auf irdische Mittel setzen, sondern vertrauensvoll alles von dir erwarten. Darum bitten wir durch Jesus Christus."
