    "25": {
      "n1": {
        "name": "Ludwig IX. von Frankreich",
        "genitiv": "Genitiv",
        "Lesehore": {
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "patr_autor": "Ludwig IX. von Frankreich ( 1270)",
          "patr_werk": "Aus dem Testament an seinen Sohn.",
          "patr_titel": "Ein gerechter Knig richtet das Land auf",
          "patr_text": "Lieber Sohn, an erster Stelle mchte ich dich lehren, den Herrn, deinen Gott, aus deinem ganzen Herzen und aus allen Krften zu lieben; denn ohne das gibt es kein Heil.^l^lMein Sohn, du sollst dich vor all dem hten, wovon du weit, dass es Gott missfllt, das heit vor jeder schweren Snde. Eher sollst du dich jeder Art von Martyrium unterwerfen als eine Todsnde begehen.^l^lWenn Gott zulsst, dass dich ein Unglck trifft, musst du es bereitwillig ertragen und bedenken, dass es dir zum Guten ausschlgt und dass du es vielleicht durchaus verdient hast. Schenkt Gott dir aber Wohlergehen, sollst du ihm demtig danken. Hte dich, dass du nicht schlechter wirst, sei es durch eitle Ruhmsucht oder auf irgendeine andere Weise; denn du darfst nicht mit den Gaben Gottes gegen Gott streiten oder ihn beleidigen.^l^lGern und fromm hre den Gottesdienst der Kirche. Wenn du im Gotteshaus bist, hte dich umherzuschauen, und fhre keine leeren Gesprche. Vielmehr bete zu Gott mit Worten des Mundes oder in der Betrachtung des Herzens. Dein Herz sei gtig gegen die Armen, Elenden und Betrbten. Komm ihnen nach Mglichkeit entgegen und trste sie! Danke Gott fr alle Gaben, die er dir verliehen hat, damit du noch grerer wrdig wirst.^l^lSei gerecht gegenber deinen Untertanen. Halte den Weg der Gerechtigkeit ein, und weiche nicht zur Rechten oder zur Linken ab! Schlag dich stets eher auf die Seite der Armen als auf die der Reichen, bis du sicher bist, die Wahrheit gefunden zu haben. Sei sorgfltig darauf bedacht, dass alle deine Untertanen im Schutz der Gerechtigkeit und des Friedens leben knnen, besonders kirchliche Personen und Ordensleute.^l^lSei unserer Mutter, der rmischen Kirche, und dem Papst als dem geistlichen Vater ergeben und gehorsam. Bemhe dich, dass aus deinem Land alle Snden verschwinden, besonders Gotteslsterung und Irrlehre.^l^lLieber Sohn, ich gebe dir jeden Segen, den ein liebender Vater seinem Sohn geben kann. Die heilige Dreieinigkeit und alle Heiligen mgen dich vor allem Bsen bewahren. Der Herr gebe dir die Gnade, seinen Willen ganz zu erfllen, so dass du ihm dienst und ihn ehrst und dass wir nach diesem Leben gemeinsam die Gnade erlangen, ihn ohne Ende zu schauen, zu lieben und zu loben. Amen.",
          "patrresp_1": "Er tat, was gut war vor dem Herrn;",
          "patrresp_2": "keiner war ihm gleich unter allen Knigen.",
          "patrresp_3": "Er hing dem Herrn an und wich nicht von seinen Spuren.",
          "oration": "Gott, du hast den heiligen Knig Ludwig aus der Verantwortung fr sein Land in das himmlische Reich gerufen. Seine Frsprache helfe uns, die irdischen Pflichten so zu erfllen, dass wir zum Himmelreich gelangen. Darum bitten wir durch Jesus Christus."
        },
        "Laudes": {
          "name": "Ludwig IX. von Frankreich",
          "genitiv": "Genitiv",
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "oration": "Gott, du hast den heiligen Knig Ludwig aus der Verantwortung fr sein Land in das himmlische Reich gerufen. Seine Frsprache helfe uns, die irdischen Pflichten so zu erfllen, dass wir zum Himmelreich gelangen. Darum bitten wir durch Jesus Christus."
        },
        "Vesper": {
          "name": "Ludwig IX. von Frankreich",
          "genitiv": "Genitiv",
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "oration": "Gott, du hast den heiligen Knig Ludwig aus der Verantwortung fr sein Land in das himmlische Reich gerufen. Seine Frsprache helfe uns, die irdischen Pflichten so zu erfllen, dass wir zum Himmelreich gelangen. Darum bitten wir durch Jesus Christus."
        }
      },
      "n2": {
        "name": "Josef von Calasanza",
        "genitiv": "Genitiv",
        "Lesehore": {
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "patr_autor": "Josef von Calasanz ( 1648)",
          "patr_werk": "Aus einer Denkschrift an Kardinal Tonti.",
          "patr_titel": "Lasst uns danach trachten, Christus anzuhangen und ihm allein zu gefallen",
          "patr_text": "Es ist ein heiliger Dienst, Kinder zu erziehen, besonders die Kinder der Armen, und sie so zu belehren, dass sie das ewige Leben erlangen knnen. Dieser Dienst besitzt eine hohe Wrde und findet groen Lohn; jedermann wei es.^l^lWenn wir nmlich die Kinder unterrichten und sie vor allem in christlicher Frmmigkeit und Lehre erziehen, sorgen wir fr ihr Heil an Leib und Seele, und wir leisten ihnen gewissermaen den nmlichen Dienst wie ihre Schutzengel.^l^lDieser Dienst ist fr diejenigen Menschen, welchen Geschlechtes und Standes sie auch sein mgen, eine ausgezeichnete Hilfe. Er zieht sie vom Bsen ab und hlt sie zu guten Taten an. Junge Menschen werden mit solcher Hilfe derart zum Guten verwandelt, dass man in den so Erzogenen die Zglinge von frher nicht mehr wiedererkennt. Junge Menschen kann der Erzieher leicht dahin fhren, wohin er ihren Geist fhren mchte; wenn wir sie erst verhrten lassen, wird die Mglichkeit, sie zu biegen, stark verringert oder zuweilen ganz aufgehoben.^l^lWenn wir den Kindern, besonders den armen, eine passende Erziehung angedeihen lassen, mehrt das ihre menschliche Wrde. Die ganze menschliche und christliche Gesellschaft stimmt unserem Tun zu: Die Eltern, denn sie in erster Linie freuen sich, dass ihre Kinder auf guten Wegen gefhrt werden; die Lenker der Staaten, weil sie rechtschaffene Untertanen und gute Brger gewinnen; vor allem aber die Kirche, denn die Kinder werden auf diese Weise mit grerer Reife und besserem Erfolg als Freunde Christi und Anhnger des Evangeliums in das vielfltige und vielgestaltige Leben der Kirche eingefgt.^l^lWer das Amt eines Lehrers annimmt und es mit Eifer und Gewissenhaftigkeit ausben will, braucht viel Liebe, grte Geduld und vor allem eine tiefe Demut. Dann ist er wert, dass der Herr ihn auf sein demtiges Gebet hin zu einem tchtigen Mitarbeiter der Wahrheit macht, ihn in der Ausbung der Aufgabe strkt und ihn schlielich mit der Gabe vom Himmel beschenkt nach dem Wort: Die Mnner, die viele zum rechten Tun gefhrt haben, werden immer und ewig leuchten wie die Sterne (Dan 12,3).^l^lDas werden sie leichter erlangen, wenn sie sich durch ein Gelbnis zum immerwhrenden Dienst verpflichten, Christus anhngen und sich mhen, ihm in allem zu gefallen: Was ihr fr einen meiner geringsten Brder getan habt, das habt ihr mir getan (Mt 25,40).",
          "patrresp_1": "Nicht nur am Evangelium Gottes lieen wir euch teilnehmen, sondern auch an unserm eigenen Leben.",
          "patrresp_2": "Ihr seid uns sehr lieb geworden.",
          "patrresp_3": "Ihr seid meine Kinder, fr die ich von neuem Geburtswehen leide.",
          "oration": "Gtiger Gott, du hast den heiligen Josef von Calasanz zu einem Lehrer der Weisheit gemacht und ihn befhigt, sich mit Liebe und Geduld der Erziehung der Jugend zu widmen. Steh uns bei, wenn wir nach seinem Vorbild uns bemhen, junge Menschen zur Erkenntnis der Wahrheit zu fhren. Darum bitten wir durch Jesus Christus."
        },
        "Laudes": {
          "name": "Josef von Calasanza",
          "genitiv": "Genitiv",
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "oration": "Gtiger Gott, du hast den heiligen Josef von Calasanz zu einem Lehrer der Weisheit gemacht und ihn befhigt, sich mit Liebe und Geduld der Erziehung der Jugend zu widmen. Steh uns bei, wenn wir nach seinem Vorbild uns bemhen, junge Menschen zur Erkenntnis der Wahrheit zu fhren. Darum bitten wir durch Jesus Christus."
        },
        "Vesper": {
          "name": "Josef von Calasanza",
          "genitiv": "Genitiv",
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "oration": "Gtiger Gott, du hast den heiligen Josef von Calasanz zu einem Lehrer der Weisheit gemacht und ihn befhigt, sich mit Liebe und Geduld der Erziehung der Jugend zu widmen. Steh uns bei, wenn wir nach seinem Vorbild uns bemhen, junge Menschen zur Erkenntnis der Wahrheit zu fhren. Darum bitten wir durch Jesus Christus."
        }
      }
    },
