          "oration": "Allmchtiger Gott, der heilige Einsiedler Gunther hat den Menschen als Helfer in der Not und als Friedensstifter gedient. Gib, dass wir aus christlichem Geist unseren Brdern in selbstloser Liebe begegnen und stets fr den Frieden eintreten. Darum bitten wir durch Jesus Christus."
