    "26": {
      "n1": {
        "name": "",
        "genitiv": "Genitiv",
        "Lesehore": {
          "comm_1": "Hirten der Kirche",
          "comm_2": "Comm_2",
          "patr_autor": "Augustinus ( 430)",
          "patr_werk": "Zu Psalm 127 (126).",
          "patr_titel": "Der Herr baut das Haus und bewacht die Stadt",
          "patr_text": "Der Herr baut das Haus, der Herr Jesus Christus erbaut sein Haus. Viele arbeiten an dem Bau, aber wenn er es nicht baut, mht sich jeder umsonst der daran baut. Wer sind sie, die sich mhen am Bau? Alle, die in der Kirche das Wort Gottes verknden, die Ausspender der Sakramente Gottes.^l^lWir also reden auen, er baut innen. Wir nehmen wahr, wie ihr zuhrt; was ihr aber denkt, wei allein er, der eure Gedanken sieht. Er baut, er ermahnt, er versetzt in Furcht, er ffnet das Verstndnis, er fhrt euren Geist und Sinn zum Glauben. Und doch arbeiten auch wir als Bauleute; doch wenn nicht der Herr das Haus baut, mht sich jeder umsonst, der daran baut.^l^l(Das neue Jerusalem) hat auch Wchter. Wie es Bauleute hat, die sich um seine Auferbauung mhen, so hat es auch solche, die es bewachen, und zwar tun das die Bischfe. Darum ist der Platz der Bischfe erhht, damit sie die bersicht haben und gleichsam ber dem Volk wachen. Das griechische epskopos heit bersetzt Beaufsichtiger, weil er die Aufsicht hat, weil er von oben herab sieht. ber diesen erhhten Platz wird eine gefhrliche Rechenschaft verlangt, wenn wir hier nicht in einer solchen Gesinnung stehen, dass wir demutsvoll den Platz zu euren Fen einnehmen und fr euch beten, dass er euch behte, der eure Gesinnungen kennt.^l^lDenn wir knnen euch zwar hereinkommen und hinausgehen sehen; aber was ihr in euren Herzen denkt, sehen wir um so weniger, als wir nicht einmal sehen knnen, was ihr in euren Husern tut. Wie fhren wir denn dann die Aufsicht? Nach Menschenweise, soviel wir knnen, soviel wir empfangen haben. Wenn wir aber nach Menschenweise Aufsicht fhren und nur unvollkommen Wache halten knnen, bleibt ihr darum ohne Aufsicht? Das sei fern. Denn wo bliebe der, von dem es heit: Wenn nicht der Herr die Stadt bewacht, wacht der Wachter umsonst? Wir bemhen uns, Aufsicht zu fhren, doch unsere Mhe ist vergebens, wenn er nicht wacht, der eure Gedanken sieht. Er hlt Wache, wenn ihr wach seid, er hlt auch Wache, wenn ihr schlaft.^l^lWir wachen ber euch, da wir durch unser Verwaltungsamt dazu verpflichtet sind. Doch mchten wir auch zusammen mit euch beschirmt werden. Euch gegenber sind wir Hirten, aber unter jenem Hirten sind wir zusammen mit euch Schafe. Euch gegenber sind wir von diesem Platz aus Lehrer, doch unter jenem einzigen Lehrer sind wir in dieser Schule eure Mitschler.",
          "patrresp_1": "Verknde das Wort, tritt dafr ein, ob man es hren will oder nicht.",
          "patrresp_2": "Wenn du das tust, rettest du dich und alle, die auf dich hren.",
          "patrresp_3": "Weise zurecht, tadle, ermahne, in unermdlicher und geduldiger Belehrung.",
          "oration": "Allmchtiger Gott, du hast den heiligen Witta mit deiner Liebe erfllt; du hast ihm jenen Glauben geschenkt, der die Welt berwindet, und ihn aufgenommen in die Schar der heiligen Bischfe. Mache auch uns auf seine Frsprache treu im Glauben und beharrlich in der Liebe, damit wir an seiner Herrlichkeit Anteil erlangen. Darum bitten wir durch Jesus Christus."
        },
        "Laudes": {
          "genitiv": "Genitiv",
          "comm_1": "Hirten der Kirche",
          "comm_2": "Comm_2",
          "oration": "Allmchtiger Gott, du hast den heiligen Witta mit deiner Liebe erfllt; du hast ihm jenen Glauben geschenkt, der die Welt berwindet, und ihn aufgenommen in die Schar der heiligen Bischfe. Mache auch uns auf seine Frsprache treu im Glauben und beharrlich in der Liebe, damit wir an seiner Herrlichkeit Anteil erlangen. Darum bitten wir durch Jesus Christus."
        },
        "Vesper": {
          "genitiv": "Genitiv",
          "comm_1": "Hirten der Kirche",
          "comm_2": "Comm_2",
          "oration": "Allmchtiger Gott, du hast den heiligen Witta mit deiner Liebe erfllt; du hast ihm jenen Glauben geschenkt, der die Welt berwindet, und ihn aufgenommen in die Schar der heiligen Bischfe. Mache auch uns auf seine Frsprache treu im Glauben und beharrlich in der Liebe, damit wir an seiner Herrlichkeit Anteil erlangen. Darum bitten wir durch Jesus Christus."
        }
      }
    },
    "31": {
      "n1": {
        "name": "",
        "genitiv": "Genitiv",
        "Lesehore": {
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "patr_autor": "Hieronymus ( 419/420)",
          "patr_werk": "Aus dem Brief an Oceanus.",
          "patr_titel": "Die Eigenschaften eines Bischofs",
          "patr_text": "Wer das Amt eines Bischofs anstrebt, der strebt nach einer groen Aufgabe (1 Tim 3,1). Der Apostel denkt also an eine Aufgabe, nicht an eine Wrde; er fasst die Arbeit, nicht das Vergngen ins Auge. Er spricht von einer Aufgabe, die zur Demut hinabfhrt und seinen Inhaber nicht vor Stolz sich aufblhen lsst.^l^lDer Bischof soll ein Mann ohne Tadel sein (1 Tim 3,2), oder wie es im Brief an Titus heit: Er soll unbescholten sein (Tit 1,6). Hier fasst der Apostel mit einem Ausdruck alle Tugenden zusammen und stellt damit eine Forderung auf, die beinahe ber die natrlichen Krfte hinausgeht. Denn wenn jede Snde, selbst ein unntzes Wort, Tadel verdient: wo in aller Welt gibt es dann einen Menschen, der ohne Snde und damit ohne Tadel wre?^l^lDer Apostel will, dass nur ein solcher zum zuknftigen Hirten der Kirche ausersehen werde, nach dem sich die anderen richten knnen, um zur Herde im wahren Sinn zu werden. Die Rhetoren verlangen zwei Dinge vom Redner: er muss ein guter Mensch und im Reden erfahren sein. Vor allem verlangt man eine tadellose Lebensfhrung und erst an zweiter Stelle eine beredte Zunge, will einer Anerkennung finden. Denn wo das Leben die Worte Lgen straft, hrt jede Lehrautoritt auf.",
          "patrresp_1": "Gebt acht auf die ganze Herde, in der euch der Heilige Geist zu Bischfen bestellt hat.",
          "patrresp_2": "Sorgt wie Hirten fr die Kirche Gottes, die er sich durch das Blut seines eigenen Sohnes erworben hat.",
          "patrresp_3": "Von Verwaltern verlangt man, dass sie sich treu erweisen.",
          "oration": "Gott, du hast uns im heiligen Wolfgang einen Mann des Geistes und der Tat geschenkt und ihn berufen, als Mnch und Bischof auf vielfache Weise deinem Volk zu dienen. Gib auch der Kirche unserer Zeit die Weite seines Geistes und die Kraft seiner Liebe. Darum bitten wir durch Jesus Christus."
        },
        "Laudes": {
          "genitiv": "Genitiv",
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "oration": "Gott, du hast uns im heiligen Wolfgang einen Mann des Geistes und der Tat geschenkt und ihn berufen, als Mnch und Bischof auf vielfache Weise deinem Volk zu dienen. Gib auch der Kirche unserer Zeit die Weite seines Geistes und die Kraft seiner Liebe. Darum bitten wir durch Jesus Christus."
        },
        "Vesper": {
          "genitiv": "Genitiv",
          "comm_1": "Comm",
          "comm_2": "Comm_2",
          "oration": "Gott, du hast uns im heiligen Wolfgang einen Mann des Geistes und der Tat geschenkt und ihn berufen, als Mnch und Bischof auf vielfache Weise deinem Volk zu dienen. Gib auch der Kirche unserer Zeit die Weite seines Geistes und die Kraft seiner Liebe. Darum bitten wir durch Jesus Christus."
        }
      }
    }
