          "oration": "Gtiger Gott, du hast den seligen Priester Rupert zu einem standhaften Bekenner des Glaubens und selbstlosen Helfer der Armen gemacht. Erwecke auf seine Frsprache der Kirche neue, vorbildliche Verknder des Glaubens und schenke uns allen ein offenes Herz fr die Nte der Menschen. Darum bitten wir durch Jesus Christus."
