          "oration": "Gott, du wahres Licht, du hast den Blinden und Augenkranken in der heiligen Odilia eine himmlische Patronin gegeben. ffne uns auf ihre Frsprache die Augen, damit wir in der geschaffenen Schnheit deine Gre erahnen und dich einst schauen in deinem himmlischen Licht. Darum bitten wir durch Jesus Christus."
