import React from 'react';

const formatBibleRef = (text) => {
    // Ursprnglichen Text fr Fehlerflle speichern
    const originalText = text;

    // Text bereinigen: Leerzeichen entfernen und Bindestriche durch Gedankenstriche ersetzen
    text = text.replace(/-/g, '');

    let result = [];        // Array fr die React-Elemente
    let currentText = '';   // Puffer fr den aktuellen Text
    let inVerseSection = false;

    // Hilfsfunktion zum Hinzufgen des aktuellen Texts zum Result-Array
    const addCurrentText = () => {
        if (currentText) {
            if (inVerseSection) {
                result.push(
                    <span class='formatVerse' key={`verse-${result.length}`}>
                        {currentText}
                    </span>
                );
            } else {
                result.push(currentText);
            }
            currentText = '';
        }
    };

    for (let i = 0; i < text.length; i++) {
        const char = text[i];

        // Nchstes Satzzeichen finden (wenn vorhanden)
        const nextPunctuation = text.slice(i + 1).match(/[,\.;]/);
        const nextPunctuationType = nextPunctuation ? nextPunctuation[0] : null;

        switch (char) {
            case ',':
                if (inVerseSection) {
                    // Fehler: Komma whrend eines Vers-Abschnitts
                    return originalText;
                }
                addCurrentText();
                inVerseSection = true;
                currentText = ',';
                break;

            case '.':
                if (!inVerseSection) {
                    // Fehler: Punkt auerhalb eines Vers-Abschnitts
                    return originalText;
                }
                currentText += '.';
                break;

            case ';':
                if (!inVerseSection) {
                    // Fehler: Semikolon auerhalb eines Vers-Abschnitts
                    return originalText;
                }
                currentText += ';';
                addCurrentText();
                inVerseSection = false;
                result.push(' ');
                break;

            case '':
                if (!inVerseSection) {
                    // Fall 1: Gedankenstrich auerhalb eines Vers-Abschnitts
                    addCurrentText();
                    currentText = '';
                } else {
                    if (!nextPunctuationType) {
                        // Fall 3: Kein weiteres Satzzeichen folgt
                        currentText += '';
                    } else if (nextPunctuationType === ',') {
                        // Fall 2: Nchstes Satzzeichen ist Komma
                        currentText += '';
                        addCurrentText();
                        inVerseSection = false;
                        result.push(' ');
                    } else if (nextPunctuationType === '') {
                        // Fall 4: Nchstes Satzzeichen ist Gedankenstrich
                        return originalText;
                    } else {
                        // Fall 3: Nchstes Satzzeichen ist Punkt oder Semikolon
                        currentText += '';
                    }
                }
                break;

            default:
                currentText += char;
        }
    }

    // Restlichen Text hinzufgen
    addCurrentText();

    return result.length > 0 ? <>{result}</> : originalText;
};

export default formatBibleRef;