        "notes": "fHinweise:0fhsFr die folgenden beiden Wochen kann das 1992 erschienene Stundenbuch und das 1996 erschienene Messbuch fr Karwoche und Osteroktav verwendet werden.hsVon Sonntag bis Donnerstag gibt es im Offizium feigene Hymnen, Antiphonen und Texte von der Kurzlesung an0f.hsDas fGedchtnis des Einzuges unseres Herrn in Jerusalem0f soll fam Palmsonntag0f in allen Messen seinen Ausdruck finden: durch Prozession oder feierlichen Einzug vor dem Hauptgottesdienst, durch einfachen Einzug vor den anderen Messen. Der feierliche Einzug, nicht aber die Prozession kann wiederholt werden vor der einen oder anderen Messe mit groer Teilnehmerzahl. Der Priester trgt bei der Prozession Messgewand oder Vespermantel in Rot. Ritus siehe MBI[1]ff bzw. MBO3ff.hsfKleinere Gottesdienstgemeinschaften mgen sich zur Feier des Triduum Paschale greren anschlieen0f (FOV43).hsfVon Grndonnerstag bis Ostermontag einschl. sind Messen ohne Gemeinde0f (vgl. FOV 47) (Privatzelebration, Messen fr Einzelne oder kleine Gruppen) fund Beerdigungsmessen nicht gestattet0f (vgl. AEM336). ber einen eigenen Wortgottesdienst bei einer Beerdigung s.o.Kap.III.D.8a.hsfDie Kirche empfiehlt den recht vorbereiteten und ehrfrchtigen Kommunionempfang bei jeder hl.Messe. Jeder Glubige aber soll wenigstens einmal im Jahr, nach Mglichkeit in der sterlichen Zeit0f (zwischen Aschermittwoch und Pfingsten), fdie hl. Kommunion empfangen0f und durch diese volle Teilnahme an der Eucharistie seine Gemeinschaft mit der Kirche sichtbar machen (vgl. Weisung d. dt. Bischfe zur kirchl. Bupraxis, 24.11.1986, zuletzt verffentlicht in: Handbuch Bistum Fulda, Gruppe7, Fundstelle3.6, S.5, letzter Abschnitt; can.920CIC; KKK1417). hsfAm Ostersonntag im Hohen Dom zu Fulda Pontifikalamt mit Apostolischem Segen0f.hsDer frher am Palmsonntag begangene dizesane Weltjugendtag wurde von Papst Franziskus auf den jhrlichen Christknigssonntag verlegt. Heute wird er nicht mehr gefeiert.hsDie fKommemoration des hl. Martin I.0f entfllt in diesem Jahr.h",
