        "notes": "fHinweise:0fhsNach ltester berlieferung der Kirche sind heute alle Messen ohne Gemeinde untersagt (FOV47), also fkeine Privatzelebration, keine Messen fr Einzelne oder fr kleine Gruppen0f.hsAm Grndonnerstag darf fkeine Begrbnismesse0f gefeiert werden (vgl.AEM336). hsfDen Kranken darf0f whrend des ganzen Triduum Paschale an allen Tagen fdie hl. Kommunion gereicht werden0f, fam Karsamstag jedoch nur in Todesgefahr0f.hsfAm Grndonnerstag sind Altarkreuz(e) und Prozessionskreuz wei verhllt0f. hsDie fMesse vom Letzten Abendmahl wird am Abend zwischen 16Uhr und 21Uhr gefeiert0f, zu einer Stunde, die fr die Teilnahme der gesamten Ortsgemeinde am geeignetsten ist (FOV46). In ihr sollen fgengend Hostien auch fr die Kommunionspendung am Karfreitag konsekriert0f werden.hsfDie Orgel kann alle Lieder und Gesnge bis einschlielich des Gesanges zur bertragung des Allerheiligsten Pange lingua begleiten0f, danach schweigt sie bis vor dem Gloria der Osternachtfeier, das dann wieder mit der Orgel begleitet wird (vgl. hierzu: SCHOTT Liedvorschlge aus dem Gotteslob, hrsg. von Kurt Grahl, Freiburg2014, S.42 unten).hsfDie Fuwaschung in den Abendmahlsfeiern0f kann nicht nur an Jungen und Mnnern, sondern auch an Mdchen und Frauen vorgenommen werden (Dekret der KGS vom 6.Januar2016 von Papst Franziskus)hsfNach dem Abendmahlsamt mgen die Glubigen eine angemessene Zeit hindurch nchtliche Anbetung vor dem hl.Sakrament (ohne Monstranz!) halten0f, die nach Mitternacht jedoch ohne jede Feierlichkeit sein soll. flbergandachten und Anbetungsstunden0f werden nach rtlichem Brauch gehalten. Zur Andacht: GL286; GL674v,20v mit GL675v,3+6+80v; GL676v,10v; Abschluss: GL682v,10v.hsfEin vollkommener Ablass0f (s.o.Kap.I.C.10.) kann unter den blichen Bedingungen (Beichte, entschlossene Abkehr von jeder Snde, Kommunionempfang, Gebet in den Anliegen des Papstes) gewonnen werden, wenn man fnach der Abendmahlsmesse das Tantum ergo andchtig betet oder singt0f.h",
