        "sec_liturgy": "wtfMs vom Letzten Abendmahl0f, wGl mit fLuten der Glocken0f (die danach schweigen bis in die Ostervigil; die Orgel kann bis zum Gesang bei der bertragung einschl. spielen (s.o.)), 0wwHomilie, fFuwaschung0f nach dem Ev empfohlen, EuchPrfIoderII, inden HgI_III eigEinschbe (MBI[26_37] bzw. MBO32_62). 0wwfKeineEntlassriten0f, sondern nach dem Schlussgebet fbertragung des hl.Sakramentes0f fr die Kommunionfeier des Karfreitags an einen geeigneten Ort, fEntblung des Altars0f ohne besonderen Ritus, jedoch nur dort, wo auch die Liturgiefeier am Karfreitag gehalten wird, fEntfernen der Kreuze0f aus der Kirche fund des Weihwassers0f aus den Weihwasserbecken an den Eingngen.0wlVp entfllt fr die Teilnehmer der Abendmahlsfeier.lf2.KpvomSo0f (statt Resp die Ant Christus)p"
