        "post_notes": "fOSTERZEIT0fjDie Zeit der fFnfzig Tage vom Sonntag der Auferstehung bis Pfingstsonntag0f wird als fder groe Tag des Herrn0f gefeiert. In diesen Tagen wird vor allem das fHalleluja0f gesungen, fkder0k Gesang der Osterzeit0f (vgl.GOK22); er sollte in dieser Zeit neben den charakteristischen fOsterliedern mit Halleluja0f (GL318, 322, 324, 326, 328, 331, 332, 333, 337) als feigener Hallelujaruf0f in Erscheinung treten, etwa als Kehrvers zum Antwortpsalm, als Ruf vor dem Evangelium (GL174_176) und als sterlicher Entlassungsruf: Gehet hin in Frieden, Halleluja, Halleluja  in der gesungenen Form. fDie ersten acht Tage der Osterzeit bilden die Osteroktav und werden wie Hochfeste des Herrn begangen; whrend der Osteroktav Komplet vom So nach der 1. oder 2.Vp mit eigenem Responsorium0f. fMarianische Antiphon whrend der ganzen Osterzeit: Regina caeli0f (GL666v,30v) oder fFreu dich, du Himmelsknigin0f (vgl.GL525).hhfLektionar zum StB I/30fh",
