        "notes": "fHinweise:0fhsfDie Osternachtmesse ist die eigentliche Osterfeier0f. Mit der Teilnahme an dieser Messe ist die sogenannte Sonntagspflicht erfllt, ganz gleich, ob die Osternachtfeier am Abend, um Mitternacht oder am Morgen stattfindet.hsfDie ganze Feier der Osternacht findet in der Nacht statt0f; sie soll entweder nicht vor Einbruch der Dunkelheit beginnen oder nicht nach der Morgendmmerung des Sonntags enden. fDiese Vorschrift ist streng einzuhalten.0f Gegenteilige Missbruche und Gewohnheiten, die sich hier und dort eingebrgert haben, nmlich die Osternacht zu der Zeit zu feiern, zu der man die Vorabendmesse des Sonntags zu halten pflegt, werden verworfen (FOV78; MBO101 und MBI[63], Hinweis Nr.3; CE333). fIn diesem Jahr dauert die Dunkelheit ungefhr von 21.45Uhr am Samstagabend bis Sonntag frh um 5.30Uhr0f.hsEs soll dafr gesorgt werden, dass alle Glubigen, die an der Osternachtfeier teilnehmen, fKerzen fr die Erneuerung des eigenen Taufversprechens0f haben.hsDas feierliche fHalleluja in der Osternacht0f soll dreimal vom Zelebranten mit aufsteigendem Ton gesungen werden. Nach dem dritten Mal folgen die Psalmverse und darauf wieder das Halleluja, vom Volk gesungen (CE352).hsfNeugetaufte Erwachsene sollen die hl. Kommunion unter beiden Gestalten empfangen0f und knnen vorweg besonders dazu eingeladen werden (CE370).hsfSpeisen (Brot, Eier, Fleisch)0f knnen nach dem Schlussgebet gesegnet werden, s. MBO174 oder BenediktionaleNr.7,S.58f.hsfEin vollkommener Ablass0f (s.o.Kap.I.C.10.) kann unter den blichen Bedingungen (Beichte, entschlossene Abkehr von jeder Snde, Kommunionempfang, Gebet in den Anliegen des Papstes) gewonnen werden, wenn man whrend der Feier der Osternacht sein fTaufbekenntnis0f nach einer rechtmig approbierten Formel erneuert.hsfEin vollkommener Ablass0f ist ebenso verbunden mit dem fEmpfang des Apostolischen Segens0f, den der Bischof heute Vormittag im Pontifikalamt im Dom zu Fulda spendet, und mit dem fEmpfang des Ppstlichen Segens Urbi et orbi (auch ber Fernsehen, Radio oder Internet)0f, den der Heilige Vater heute Mittag in Rom spendet.hsZur fOsterzeit0f kann eine fStatue des auferstandenen Christus0f an einem dafr geeigneten Platz im Altarraum oder in der Kirche aufgestellt werden.h",
