        "notes": "fHinweis:0f fHeute0f wird im Dom zu Fulda das fPontifikalamt mit Spendung der hl. Priesterweihe0f gefeiert. fIn den Frbitten der Sonntagsmessen mge im ganzen Bistum auch fr die Neupriester gebetet werden.0f Glubige, besonders auch Jugendliche, sind zur Mitfeier der Priesterweihe nach Fulda eingeladen.sfZum Herz-Mari-Sa:0f Die Votivmesse ist nur bedingt erlaubt (s.o.Kap.III.D.3.).h",
