        "prayerTexts": "",
        "vig_notes": "fHOCHHEILIGES PFINGSTFEST0fjfHinweise:0fhsDie fVorabendmesse zu Pfingsten0f kann durch eine vermehrte Zahl der Schriftlesungen zu einer fPfingstvigil0f erweitert werden (HR,S.5_9/MBkl1228_1232). Diese Pfingstvigil kann auch als eigener Wortgottesdienst gehalten werden.hsAus pastoralen Grnden knnen in der Vigilmesse auch die Perikopen vom Pfingstsonntag genommen werden.hsfMorgen endet die Zeit der Osterkommunion0f.hsZum fEnde der Osterzeit am Pfingstsonntagabend0f empfiehlt es sich, der fOsterkerze0f einen wrdigen fPlatz beim Taufbecken0f zu geben. An ihr werden in der Tauffeier die Taufkerzen entzndet. Bei Begrbnissen kann die Osterkerze an den Sarg oder bei Begrbnismessen in den Altarraum gestellt und angezndet werden (vgl. FOV99). Auer bei diesen beiden besonderen Feiern darf sie jedoch auerhalb der Osterzeit nicht angezndet werden und auch nicht im Altarraum stehen.hsfEin vollkommener Ablass0f (s.o.Kap.I.C.10.) kann unter den blichen Bedingungen (Beichte, entschlossene Abkehr von jeder Snde, Kommunionempfang, Gebet in den Anliegen des Papstes) gewonnen werden, wenn man heute Abend oder morgen in einer Kirche oder Kapelle andchtig dem feierlichen Gesang des fHymnus Veni, Creator Spiritus0f (GL341) bzw. fKomm, Heilger Geist0f (GL342) beiwohnt.h",
