        "notes": "fHinweise:0fhsHeute wird zum 5. Mal der fWelttag der Groeltern und Senioren0f begangen. Papst Franziskus hat ihn am 31. Januar 2021 ausgerufen und  in zeitliche Nhe zum Gedenktag der hll. Joachim und Anna, der Groeltern Jesu  auf den 4. Sonntag im Juli gelegt. Zur Feier des Welttages mgen das jhrliche Leitwort und verffentlichte Materialien (Vatikan, DBK) bercksichtigt werden.hsAm heutigen Sonntag kann vom Mittag des Vortages an der fPortiunkulaablass0f gewonnen werden, auch am 2. August oder dem darauffolgenden Sonntag vom Mittag des Vortages an (vgl. Hinweis vor dem 20.Juli).h",
