        "notes": "fHinweise:0fhsPapst Franziskus hat angeregt, jhrlich einen fGebetstag fr Opfer sexuellen Missbrauchs0f zu begehen. Die Deutsche Bischofskonferenz hat fr Deutschland erstmals 2018 festgelegt, diesen fGebetstag im zeitlichen Umfeld des durch den Europarat initiierten Europischen Tages zum Schutz von Kindern vor sexueller Ausbeutung und sexuellem Missbrauch0f zu begehen. Dieser findet bereits seit 2015 jeweils fam 18. November0f statt. Ziel des europischen Tages ist es, Impulse fr einen verbesserten Kinderschutz zu geben und die Gesellschaft weiterhin fr die Thematik des sexuellen Kindesmissbrauchs zu sensibilisieren. Mit dem Gebetstag fr die Opfer sexuellen Missbrauchs untersttzen die deutschen Bischfe das Anliegen von Papst Franziskus, der den nationalen Bischofskonferenzen seine Bitte zur Einrichtung eines Tages des Gebetes und der Bue fr die Opfer sexuellen Missbrauchs bermittelt hatte. Liturgische Materialien sind unter www.dbk.de/themen/sexueller-missbrauch/gebetstag/ abrufbar.hsfZum heutigen Gedenktag der Weihe der Basiliken St. Peter und St. Paul in Rom0f eignet sich das Lied fGL873, bes. Str.3+40f, auch nach Melodie GL280 singbar.h",
