        "prayerTexts": "",
        "vig_notes": "vor einer etwaigen Vorabendmesse und der Vesper:lfEnde des alten Kirchenjahres0fjfADVENTSZEIT0fjDie Adventszeit hat einen dreifachen Charakter. Sie ist erstens Vorbereitungszeit auf die weihnachtlichen Hochfeste mit ihrem fGedchtnis des ersten Kommens des Gottessohnes0f zu den Menschen. Zweitens ist sie eine besondere Zeit der fAufmerksamkeit und Wachsamkeit fr die Ankunft Christi heute0f, an jedem Tag, mitten im Alltag und im menschlichen Leben. Und drittens lenkt sie die Herzen zugleich durch dieses Gedenken an die erste Ankunft des Herrn, die Vorbereitung auf Weihnachten und sein tgliches neues (An-)Kommen hin zur fErwartung der Ankunft Christi am Ende der Zeiten0f. Unter allen drei Gesichtspunkten ist die Adventszeit feine Zeit wachsamer, hingebender und freudiger Erwartung0f.hhfBeginn des neuen Kirchenjahres0flmit der 1. Vesper bzw. der Vorabendmesse zum1.AdventssonntagjEsbeginntdasfLesejahrA0flfrdieMessperikopenanden Sonntagen(fMLA/I0f).hEsbeginntdasfLesejahrII0flderLektionar-FaszikelzumStB (fLektionarzumStBII/10f).hPerikopen an den Wochentagen: fMLIV0f.hfStB0f und fLitHor0f: fBand I0f.hfHinweise:0fhsfDie Sonn- und Wochentage des Advent0f haben jeweils ein eigenes Offizium. Vor dem Off des 1.AdvSo sind Hymnen fr den ersten Teil der Adventszeit (bis 16.Dezember) angegeben (StBI3ff).hsAm Schluss der Komplet steht in der Advents- und Weihnachtszeit die Marianische Antiphon fAlma Redemptoris Mater0f (GL666v,10v) bzw. fErhabene Mutter des Erlsers0f (vgl.GL530).hsfDas Gloria entfllt an den Sonntagen der Adventszeit.0fhsSofern nichts anderes angegeben ist, wird bis zum 16.Dez. in der Regel eine der AdventsprfationenI_IV genommen, vom 17.bis 24.Dez. aber vor allem AdventsprfationV.hsDie fMarienmesse im Advent (Roratemesse)0f ist unter den in der Einfhrung zum Direktorium  (s.o.Kap.III.D.3.) genannten Voraussetzungen bis zum 16.Dez. gestattet auer an Sonntagen, Hochfesten und Festen. MB890f mit den laufenden Tageslesungen, oder MBm und MLm Nrr.1_3; Farbe: wei.hsIn der kommenden Woche werden die fWinterquatember0f an einem oder mehreren Tagen (nach Wahl) begangen als Tag(e) religiser Erneuerung. fThema: Friede0f. Vorschlag fr die Wahl des Messformulars: Quatembermesse, MB265, oder Nr.17, 21 oder22 aus den Messen fr besondere Anliegen (MB1054_1059/1078_1084), AdvPrf, Farbe: violett. LL und Evv: Perikopen fr besondere Anliegen, MLVIII201_217.hsfDer Blumenschmuck und die Musik0f mgen im Advent sehr gemigt sein. Die Musik soll auf Weihnachten vorbereiten.hsZur fSegnung des Adventskranzes/der (mitgebrachten) Adventskrnze0f s.BenediktionaleNr.1,S.25ff oder GL24.h",
