        "notes": "fHinweise:0fhsHeute darf jeder Priester dreimal (kon)zelebrieren, jedoch nur zur jeweils entsprechenden Zeit: die erste Messe in der Nacht (Christmette), die zweite am Morgen (Hirtenmesse), die dritte am Tag (Festhochamt).hsfEin vollkommener Ablass0f (s.o.Kap.I.C.10.) ist unter den blichen Bedingungen (Beichte, entschlossene Abkehr von jeder Snde, Kommunionempfang, Gebet in den Anliegen des Papstes) verbunden mit dem fEmpfang des Apostolischen Segens0f, den der Bischof heute Vormittag im Pontifikalamt im Dom zu Fulda spendet, und mit dem fEmpfang des Ppstlichen Segens Urbi et orbi (auch ber Fernsehen, Radio oder Internet)0f, den der Heilige Vater heute Mittag in Rom spendet.hsHeute ist der fGed der hl. Anastasia von Sirmium0f, die um das Jahr 304 das Martyrium erlitten hat. Sie gehrt zu den seit alten Zeiten verehrten Heiligen, die im Rmischen Messkanon genannt werden, deren Ged heute aber nicht mehr im ARK verzeichnet ist. Durch die fWahl des HgI0f kann ihr Gedchtnis weiter geehrt werden.hsVor Beginn der Christmette kann die ffeierliche Ankndigung der Geburt des Herrn aus dem Rmischen Martyrologium0f gesungen werden (siehe Hinweis auf der Homepage des Liturgischen Institutes Trier www.liturgie.de).h",
