        "notes": "fHinweise:0fhsAm heutigen fFest der hl. Familie0f wird der fFamiliensonntag0f begangen. fLeitthema und Arbeitshilfen unter www.ehe-familie-kirche.de0f. Dizesen, Gemeinden, Verbnde und kirchliche Einrichtungen sind eingeladen, sich mit eigenen Veranstaltungen und Initiativen zum Jahresthema einzubringen und das Jahresmotto ggf.an einem anderen Sonntag im Kirchenjahr aufzugreifen.hsZum heutigen fFest der hl. Familie0f ist es angebracht, eine ffeierliche Segnung der Kinder0f zu halten. Ein Vorschlag findet sich im BenediktionaleNr.2,S.34ff. Es knnen aber auch Familien oder Familienteile gesegnet werden, etwa nach der Homilie oder im Anschluss an die hl. Messe (vgl. BenediktionaleNr.51,S.237ff: Segnung einer Familie durch den Priester).   Frbitten fr die Familien.hsDas fFest der Unschuldigen Kinder0f entfllt in diesem Jahr. Dennoch empfiehlt sich heute in den fFrbitten0f und/oder auch auerhalb der hl. Messe ffr den Schutz der ungeborenen Kinder/des menschlichen Embryos und eine neue diesbezgliche Mentalitt und Gesetzgebung0f zu beten. Gemeinsames Rosenkranzgebet in diesem Anliegen ist um 18.30 Uhr an der Mariensule unterhalb des Frauenberges Fulda.h",
